import React, { FC, useState } from 'react';
import { AltPage } from '../../components';
import Uploader from '../../components/uploader';
import PasspportFront from '../../assets/media/pass_front.png';
import PassportBack from '../../assets/media/pass_back.png';
import { useApi } from '../../misc/customHooks';
import { useNavigate } from 'react-router-dom';

const formDataDefault = new FormData();
const UploadPass: FC<{}> = props => {
    const Api = useApi();
    const [formData, setFormData] = useState<FormData>(formDataDefault);
    const [uploads, setUploads] = useState<any>({});
    const navigate = useNavigate();
    const upload = (dataKey: string, data: any) => {
        uploads[dataKey] = true;
        formData.append(dataKey, data);
        if (uploads.frontside && uploads.backside) {
            Api?.uploadPass(formData);
            navigate('/upload-selfie')
        }
        setFormData(formData);
        setUploads(uploads);
    }
    return (
        <AltPage hasHeader>
            <div className="text-center">
                <h4 className="text-xl text-custom-grey font-[600]">Для оформления кредита, сделайте фото <br/> Вашего паспорта и загрузите ниже </h4>
                <div className="my-5 mx-auto max-w-[60%]">
                    <img className="w-[100%] h-2" src="images/divider.svg" alt="divider" width="100%" />
                </div>
                <div className="flex mx-auto flex-col max-w-[350px] md:flex-row md:max-w-[800px] gap-[30px]">
                    <div className="flex-1">
                        <Uploader dataKey="frontside" onChange={upload} defaultImageUrl={PasspportFront} title="Передняя сторона паспорта" />
                    </div>
                    <div className="flex-1">
                        <Uploader dataKey="backside" onChange={upload} defaultImageUrl={PassportBack} title="Задняя сторона паспорта" />
                    </div>
                </div>
            </div>
        </AltPage>
    )
}

export default UploadPass;
