import React, { FC,  useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { AltPage, BankCard } from '../../components';
import { IBankCardProps } from '../../components/bank-card/typing';
import { IScoreKey } from '../../misc/api';
import { useApi, useStore, useUpdateStore } from '../../misc/customHooks';
import { banks as allBanks } from './static';

type IBank = Omit<IBankCardProps, 'onClick'>;

const Greetings: FC<{}> = () => {
    const Api = useApi();
    const store = useStore();
    const navigate = useNavigate();
    const updateStore = useUpdateStore();
    const [banks, setBanks] = useState<IBank[]>([]);

    const choseBank = (bankName: string) => {
        updateStore && updateStore({ loaderOn: true })
        const bankInfo = banks.find(item => item.nameEng === bankName);
        const payload = {
            data: { bank_name: bankName },
            choosenBank: {
                nameEng: bankName,
                name: bankInfo?.name
            }
        }
        Api?.chosenBank(payload);
        if (bankInfo?.passport_photo) {
            navigate('/upload');
        } else {
            navigate('/qrcode')
        }
    }

    useEffect(() => {
        const score = store?.score;
        updateStore && updateStore({ scorePassed: true });
        if (score) {
            let arr = allBanks.filter(item => score[item.nameEng as IScoreKey]);
            const bankArr = arr.map<IBank>(item => {
                const name = item.nameEng as IScoreKey;
                const bank = Object.assign({}, item) as IBank;
                bank.approval = score[name].scoring_status;
                bank.percentage = score[name].interest_rate;
                bank.monthlyPayment = score[name].monthly_payment;
                bank.passport_photo = score[name].passport_photo;
                bank.add_terms = score[name].add_terms;
                bank.commission = score[name].commission;
                return bank;
            })
            setBanks([...bankArr]);
        }
    }, [store?.score])

    return (
        <AltPage hasHeader>
            <div className="text-center py-8 max-w-[1050px]">
                <h2 className="md:text-5xl text-3xl font-bold mb-2 text-blue-500">Поздравляем!</h2>
                <div className="my-4 mx-auto max-w-[80%]">
                    <img className="w-[100%] h-2" src="images/divider.svg" alt="divider" width="100%" />
                </div>
                <p className='mb-4 md:text-lg sm:text-[14px] text-[#1D1D1F] md:leading-[1.2rem] font-[500]'>
                    Результат: Вам могут одобрить кредит в <b className="underline">{ banks.length }</b> учреждениях! <br />
                    Просмотрите варианты ниже и выберите <b>ОДНО</b> предложение.
                </p>
                <div className="grid md:grid-cols-2 md:gap-[60px] gap-[30px] md:mx-[50px] lg:mx-[150px] mx-3">
                    { banks && banks.map(item => (
                        <BankCard
                            onClick={() => choseBank(item.nameEng)}
                            key={item.name}
                            {...item}
                         />
                    )) }
                </div>
            </div>
        </AltPage>
    )
}

export default Greetings;
