import React, { FC, useState } from 'react';
import { ButtonUploader } from '../index';

interface IUploader {
    title: string;
    defaultImageUrl: string;
    dataKey: string;
    onChange: (dataKey: string, data: any) => void;
}

const Uploader: FC<IUploader> = ({ title, defaultImageUrl, dataKey, onChange }) => {

    const [imageUrl, setImageUrl] = useState(defaultImageUrl);

    const onUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files) {
            setImageUrl(URL.createObjectURL(e.target.files[0]));
            onChange(dataKey, e.target.files[0]);
        }
    }

    return (
        <div className="text-center">
            <h4 className="text-lg font-[500] text-[#1C1C1E] mb-4">{ title }</h4>
            <div className="w-[100%] min-h-[200px] md:min-h-[260px] mb-4">
                <img className="w-[100%]" src={imageUrl} alt="" />
            </div>
            <ButtonUploader onUpload={onUpload}>Загрузите фото</ButtonUploader>
        </div>
    )
}

export default Uploader;
