import React, { FC } from 'react';
import {
    IBankCardFooterProps,
    IBankCardHeaderProps,
    IBankCardProps,
    IBankCardTitleProps,
    IRangeKey,
    IRow } from './typing';

const BankCard: FC<IBankCardProps> = ({
    range,
    approval,
    onClick,
    monthlyPayment,
    percentage,
    add_terms,
    commission,
    ...titleProps
}) => {

    const staticKeys = ["Высокая", "Средняя", "Низкая"]

    const getApprovalStatus = () => {
        return [staticKeys[approval - 1], approval === 3, approval > 1];
    }

    const [approvalStatus, isLower, isLessThanHigh] = getApprovalStatus() as [string, boolean, boolean];

    return (
        <div className="text-left max-w-[350px] mx-auto">
            <BankCardTitle {...titleProps} />
            <div className="flex flex-col rounded-[30px] overflow-hidden border-4 border-[#007AFF]">
                <BankCardHeader monthlyPayment={monthlyPayment} percentage={percentage * 100} approvalStatus={approvalStatus} isLower={isLower} />
                <BankCardFooter onClick={onClick} isLower={add_terms !== '0'} сommission={commission} />
            </div>
        </div>
    )
}

/**
 * BankCardTitle
 * @param param0 
 * @returns 
 */
const BankCardTitle: FC<IBankCardTitleProps> = ({
    name, color, icon
}) => {
    return (
        <div className="flex items-center min-h-[60px] mb-3">
            <h4
                style={{ color }}
                className="font-bold text-[24px] inline-block mr-3"
            >{ name }</h4>
            <div className="max-w-[60px]">
                <img src={icon} alt="" />
            </div>
        </div>
    )
}

/**
 * BankCardHeader
 * @param param0 
 * @returns 
 */
const BankCardHeader: FC<IBankCardHeaderProps> = ({
    approvalStatus, isLower, monthlyPayment, percentage
}) => {
    return (
        <div className="w-[100%] text-white bg-[#007AFF] p-[15px] sm:p-[25px]">
            <Row
                noMargin={isLower === true}
                label="Вероятность одобрения"
                value={approvalStatus as string} />
            { !isLower && (
                <>
                    <Row label="Ежемесячное погашение" value={monthlyPayment.toFixed(0)} subvalue="сомони" />
                    <Row noMargin label="Процентная ставка" value={percentage + '%'} />
                </>
            )}
        </div>
    )
}

/**
 * BankCardFooter
 * @param param0 
 * @returns 
 */
const BankCardFooter: FC<IBankCardFooterProps> = ({ isLower, сommission, onClick }) => {

    const getInfo = (isLower: boolean) => {

        let text = "Требуется дополнительная проверка"
        if (!isLower) {
            text = "Дополнительные документы: нет"
        }

        return (
            <>
                <p>Комиссия: { сommission ? сommission + '%' : "нет"}</p>
                <p className="mb-[35px]">{ text }</p>
            </>
        )
    }

    const withPreventDefault = (e: any) => {
        e.preventDefault();
        onClick();
    }

    return (
        <div className="p-[15px] sm:p-[25px]">
            { getInfo(isLower) }
            <div className="flex">
            <a onClick={withPreventDefault} className="bg-[#007AFF] hover:opacity-75 rounded-[20px] py-1 px-[50px] mx-auto text-white">Выбрать</a>
            </div>
        </div>
    )
}

/**
 * Row
 * @param param0 
 * @returns 
 */
const Row: FC<IRow> = ({ label, value, subvalue, noMargin }) => {
    const margin = (!noMargin && "mb-3") || "";
    return (
        <div className={`flex items-center gap-[0px] ${margin}`}>
            <div className="min-w-[65%] sm:min-w-[55%] max-w-[65%] md:max-w-[55%] pr-2 md:text-[17px] text-lg leading-[1.3rem] sm:text-[22px] md:leading-[18px] sm:leading-[25px]">
                { label }:
            </div>
            <div>
                <div className="lg:text-[24px] md:text-[18px] text-lg font-bold uppercase tracking-wide leading-[30px]">
                    { value }
                </div>
                <p className='text-center font-bold text-base leading-[1rem]'>{ subvalue }</p>
            </div>
        </div>
    )
} 

export default BankCard;
