import React, { createContext, useEffect, useLayoutEffect, useState } from "react";
import { Route, Routes } from "react-router-dom/index";
import { Loader, Toast } from "./components";
import { axios, getCache } from "./misc";
import { getApi, IApi, IStore, StoreKey } from "./misc/api";
import { setCache } from "./misc/cookies";
import { Main, Scoring, Login, Profile, Review, UploadPass, Greetings, UploadSelfie, QRcode, Fallback, Auth, Logout } from "./pages";
import { pages } from "./pages/scoring/pages";

const FALLBACK_REASON = 'по причине того, что Вы не имеете прописку в г. Душанбе. На данный момент услуги платформы aion работают в пробном режиме и доступны только жителям прописанным в г. Душанбе.'


export const ContextApi = React.createContext<IApi | null>(null);
export const ContextStore = createContext<IStore | null>(null);
export const ContextUpdateStore = createContext<((store: IStore) => void) | null>(null);

const App = () => {
  const [store, setStore] = useState<IStore>({});


  const api = getApi(store, setStore);
  useLayoutEffect(() => {
    if (getCache(store)) {
      setStore({ ...store })
    }

    if (store.isAuthorized) {
      api?.getScoreData()
    }
  }, [store?.isAuthorized])

  const updateStore = (keyValue: IStore) => {
    Object.entries(keyValue).forEach(([key, value]) => {
      store[key as StoreKey] = value;
    });
    setStore({ ...store });
  };

  const { loaderOn, toastOn, toastMsg, scoreData } = store;
  return (
    <ContextApi.Provider value={api}>
      <ContextStore.Provider value={store}>
        <ContextUpdateStore.Provider value={updateStore}>
          <Routes>
            <Route path="/" element={<Main />} />
            <Route path="/scoring" element={<Scoring startPage={0} />} />
            <Route path="/login" element={<Login />} />
            <Route path="/profile" element={<Profile />} />
            <Route path="/review" element={<Review scoreData={scoreData} />} />
            <Route path="/upload" element={<UploadPass />} />
            <Route path="/greetings" element={<Greetings />} />
            <Route path="/fallback" element={<Fallback reason={store.fallbackReason}/>} />
            <Route path="/upload-selfie" element={<UploadSelfie />} />
            <Route path="/qrcode" element={<QRcode />} />
            <Route path="/auth" element={<Auth />} />
            <Route path="/logout" element={<Logout />} />
          </Routes>
          <Loader showLoader={loaderOn} />
          { toastOn && <Toast errorMsg={toastMsg as string} /> }
        </ContextUpdateStore.Provider>
      </ContextStore.Provider>
    </ContextApi.Provider>
  );
};

export default App;
