import React, { FC, useEffect, useRef, useState } from 'react';

interface IFormRate {
    onChange: (value: number) => void;
}

const FormRate: FC<IFormRate> = ({
    onChange
}) => {
    const [rate, setRate] = useState(0);
    const myRef: React.RefObject<HTMLDivElement> = useRef(null)

    const showRate = () => {
        const rates = [5, 4, 3, 2, 1];
        return rates.map(item => <div data-value={item} className={`w-[44px] h-[42px] cursor-pointer ${(item <= rate && 'bg-star') || ''}`} />)
    }

    const onSelect = (e: any) => {
        const rate = parseInt(e.target.dataset.value);
        if (rate) {
            setRate(rate)
            onChange(rate);
        }
    }

    useEffect(() => {
        if (myRef.current) {
            myRef.current.addEventListener('click', onSelect);
        }
        return () => {
            if (myRef.current) {
                myRef.current.removeEventListener('click', onSelect);
            }
        }
    })

    return (
        <div className="text-left">
            <div ref={myRef} className="flex flex-row-reverse justify-end gap-1 mb-5 rate">
                { showRate() }
            </div>
            <div className="text-lg text-grey-79">
                *Оцените платформу aion по 5 звездам. Оставьте свой фидбек, как мы можем улучшить платформу. Эта форма направляет вопросы непосредственно в техническую группу aion 
            </div>
        </div>
    )
}

export default FormRate;
