import classNames from 'classnames/bind';
import React, { FC, useState } from 'react';

interface IButtonUploader {
    children?: React.ReactNode;
    className?: string;
    onUpload: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

const ButtonUploader: FC<IButtonUploader> = ({
    children, className, onUpload
}) => {

    const [uploaderId, setUploaderId] = useState((Math.random() + 1).toString(36).substring(7))

    return (
        <>
            <input onChange={onUpload} className="invisible hidden" type="file" name="front-page" id={uploaderId} />
            <label className={classNames("block", className || "")} htmlFor={uploaderId}>
                <div className="bg-main-blue py-1 cursor-pointer hover:opacity-75 text-xl rounded-[30px] text-white">{ children }</div>
            </label>
        </>
    )
}

export default ButtonUploader;
