const SEON_EMAIL = 'https://api.seon.io/SeonRestService/email-api/v2.2/';
const SEON_PHONE = 'https://api.seon.io/SeonRestService/phone-api/v1.4/';
const SEON_IP = 'https://api.seon.io/SeonRestService/ip-api/v1.1/';
const IP_URL = 'http://ipwho.is/';

const headers = {
    "X-API-KEY": "272c8610-ff1c-4122-a9c7-82bba094dc78"
}

const getIP = async() => {
    return await fetch(IP_URL)
        .then(res => res.json())
        .then(res => res.ip)
        .catch(err => console.log(err));
}

export const collectUserInfo = async (phone: string) => {
    fetch(SEON_PHONE + phone, { headers })
        .then(res => res.json())
        .then(res => console.log(res))
        .catch(err => console.log(err));
    
    const ip = await getIP();
    fetch(SEON_IP + ip, { headers })
        .then(res => res.json())
        .then(res => console.log(res))
        .catch(err => console.log(err));
}