import { CircularProgress } from '@mui/material';
import React, { FC, useContext } from 'react';
import { ContextStore } from '../../App';

interface IToast {
    errorMsg: string;
}

const Toast: FC<IToast> = ({
    errorMsg
}) => {
    return (
        <div className="fixed text-center toast-show bg-white shadow-custom-light w-[80vw] max-w-[400px] px-[20px] py-[15px] min-h-[80px] flex items-center justify-center">
            { errorMsg }
        </div>
    )
}

export default Toast;
