import React, { useEffect, useState, useMemo } from 'react';
import { Box, Typography, Button } from '@mui/material';
import { Page, Spinner } from '../../components';
import QRCode from 'react-qr-code';
import { request, downloadQR, copy } from '../../misc';
import Countdown from 'react-countdown';
import { CountdownList } from '../../components';
import { HistoryIcon, PersonIcon, NewFileIcon } from '../../assets/media/icons';
import { ContentCopy as ContentCopyIcon, Download as DownloadIcon } from '@mui/icons-material';
import { useApi, useStore } from '../../misc/customHooks';
import { Navigate, useNavigate } from 'react-router-dom';

const Profile = () => {
    const [state, setState] = useState<any>({
        data: {},
        loading: false
    });

    const Api = useApi();
    const store = useStore();
    const navigate = useNavigate();

    useEffect(() => {
        if (!store?.isAuthorized) {
            navigate('/login')
        }
    })

    useEffect(() => {
        Api?.getProfile();
    }, []);

    const name = useMemo(() => {
        const {
            name,
            surname,
            phone_number
        } = state.data || {};
        if (name || surname) {
            return `${surname || ''} ${name || ''}`.trim();
        } else if (phone_number) {
            return phone_number;
        } else {
            return 'NO DATA';
        }
    }, [state.data]);

    const passCode = useMemo(() => state.data?.scoring_pass_code, [state.data]);

    const hasAccessToCode = useMemo(() => {
        const expireDate = state.data?.scoring_expiration_date;
        if (!!expireDate) {
            const expire = new Date(expireDate).getTime();
            const now = new Date().getTime();
            return expire > now;
        } else {
            return false;
        }

    }, [state.data]);

    const expirationTimestamp = useMemo(() => new Date(state.data?.scoring_expiration_date).getTime(), [state.data?.scoring_expiration_date]);

    return (
        <Page>
            {state.loading ?
                <Spinner/>
                :
                <div className="my-12">
                    {/*<Box textAlign="center">*/}
                    {/*    <Typography sx={{*/}
                    {/*        color: '#858584',*/}
                    {/*        fontWeight: 'bold'*/}
                    {/*    }} variant="h4">{name}</Typography>*/}
                    {/*    <Divider/>*/}
                    {/*</Box>*/}
                    <Box display="flex" flexWrap="wrap" gap={2} justifyContent="center">
                        <Box mb={2}>
                            <Box className="card--grey" textAlign="center" px="40px !important" maxWidth={320}>
                                <Typography>QR Code для получения кредита</Typography>
                                <Box
                                    borderRadius="20px"
                                    className="inline-block mb-2"
                                    sx={{ opacity: store?.qrcode ? 1 : 0.1 }}
                                    // backgroundColor="#fff"
                                    // p={2}
                                    // width={221}
                                    // height={213}
                                    // mb="36px"
                                    // mt="20px"
                                    // mx="auto"
                                >
                                    { store?.qrcode && <QRCode value={String(store?.qrcode)} color="#077AFF" level="H" id="qrcode" size={180}/> }
                                </Box>
                                {store?.qrcode && (
                                    <Button
                                        startIcon={<DownloadIcon />}
                                        className="custom--btn-white"
                                        onClick={() => downloadQR('qrcode')}
                                    >
                                        Скачать
                                    </Button>
                                )}
                            </Box>
                            {store?.qrcode && (
                                <>
                                    <Box my={2} className="card--grey" textAlign="center">
                                        <Typography>Код для получения кредита</Typography>
                                        <Typography component="b" fontSize="45px !important">{store?.qrcode}</Typography>
                                        <Box>
                                            <Button className="custom--btn-white"
                                                    startIcon={<ContentCopyIcon />}
                                                    onClick={() => copy(store?.qrcode)}>
                                                Скопировать
                                            </Button>
                                        </Box>
                                    </Box>
                                    {/* <Box mt={2} textAlign="center">
                                        <Countdown
                                            date={expirationTimestamp}
                                            renderer={props => <CountdownList {...props}
                                                                              heading="QR код будет недействителен по истичению:"/>}/>
                                    </Box> */}
                                </>
                            )}
                        </Box>
                        <Box className="pl-4 sm:pl-0">
                            <Box display="flex" className="max-w-[320px] sm:max-w-[600px]" gap={2} flexWrap={{
                                xs: 'wrap',
                                md: 'nowrap'
                            }}>
                                {
                                    store?.qrcode && (
                                        <Box alignItems="center" className="card--grey">
                                            <Typography sx={{
                                                fontSize: '22px',
                                                color: '#858584'
                                            }}>Статус aion: </Typography>
                                            <Box
                                                p="10px 20px !important"
                                                className="card--white-small"
                                                mx="auto"
                                                maxWidth={217}
                                                maxHeight={60}
                                                height="100%"
                                                display="flex"
                                                justifyContent="center"
                                            >
                                                <Box display="flex"
                                                    justifyContent="space-between">
                                                    <Typography>Одобрено</Typography>
                                                    <Box src="images/tick.svg" alt="tick" component="img"/>
                                                </Box>
                                            </Box>
                                        </Box>
                                    )
                                }
                                <Box className="card--grey">
                                    <Typography>Статус кредита</Typography>
                                    <Box display="flex" className="flex-wrap sm:flex-nowrap gap-2 sm:gap-3">
                                        <Box className="card--white-small px-[12px] sm:px-[20px] py-[6px]">
                                            <Typography>{ store?.qrcode ? 1 : 0 }</Typography>
                                            <Typography component="small">Активный</Typography>
                                        </Box>
                                        <Box className="card--white-small px-[12px] sm:px-[20px] py-[6px]">
                                            <Typography>0</Typography>
                                            <Typography component="small">Погашенный</Typography>
                                        </Box>
                                        <Box className="card--white-small px-[12px] sm:px-[20px] py-[6px]">
                                            <Typography>0</Typography>
                                            <Typography component="small">Просрочка</Typography>
                                        </Box>
                                    </Box>
                                </Box>
                            </Box>
                            {/* <Box
                                mt={3}
                                className="card--grey"
                                textAlign="center"
                                alignItems="center"
                                display="flex"
                                flexDirection="column"
                                maxWidth={710}
                            >
                                <Typography>
                                    Если вы поменяли место жительство или у вас изменились
                                    данные о доходе, загрузите, пожалуйста, свои документы и
                                    мы ещё раз рассмотрим вашу заявку
                                </Typography>
                                <Typography sx={{
                                    marginTop: '30px',
                                    marginBottom: '15px'
                                }}>
                                    Загрузите документы:
                                </Typography>
                                <Box display="flex" gap={2}>
                                    <Box className="card--white-small-15">
                                        <Typography>Справка о доходе</Typography>
                                    </Box>
                                    <Box className="card--white-small-15">
                                        <Typography>Справка о месте жительства</Typography>
                                    </Box>
                                </Box>
                            </Box> */}
                        </Box>
                        <Box display="flex" flexDirection="column" gap={3}>
                            {/* <Box className="card--grey">
                                <Typography sx={{ marginBottom: 2 }}>Доступная сумма</Typography>
                                <Box display="flex" gap={3}>
                                    <Box className="card--white-small">
                                        <Typography>3500 смн</Typography>
                                        <Typography component="small">Доступно</Typography>
                                    </Box>
                                    <Box className="card--white-small">
                                        <Typography>3500 смн</Typography>
                                        <Typography component="small">Лимит</Typography>
                                    </Box>
                                </Box>
                            </Box> */}
                            {/* <Box className="card--grey">
                                <Typography sx={{ marginBottom: 2 }}>Конвертация валют</Typography>
                                <Box display="flex" gap={3}>
                                    <Box className="card--white-small">
                                        <Typography>1 300</Typography>
                                        <Typography component="small">Сомони</Typography>
                                    </Box>
                                    <Box className="card--white-small">
                                        <Typography>100</Typography>
                                        <Typography component="small">Доллар США</Typography>
                                    </Box>
                                </Box>
                            </Box> */}
                            {/* <Box gap={2} display="flex" flexDirection="column">
                                <Button
                                    className="custom--btn-gray"
                                    startIcon={<NewFileIcon/>}
                                >
                                    Новый запрос
                                </Button>
                                <Button
                                    className="custom--btn-gray"
                                    startIcon={<HistoryIcon/>}
                                >
                                    История
                                </Button>
                                <Button
                                    className="custom--btn-gray"
                                    startIcon={<PersonIcon/>}
                                >
                                    Профиль
                                </Button>

                            </Box> */}
                        </Box>
                    </Box>
                </div>
            }
        </Page>
    );
};

export default Profile;
