import React, {  FC, useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { AltPage } from '../../components';
import { useApi, useStore } from '../../misc/customHooks';

type EventHandler = (e: any) => void;

const Auth = () => {
    const myRef: React.RefObject<HTMLDivElement> = useRef(null);
    const store = useStore();
    const Api = useApi();
    const phoneNumber = store?.phoneNumber;
    const navigate = useNavigate();
    const [activeResend, setActiveResend] = useState(false);

    const goToScoring = async ()  => {
        await Api?.getScoreData();
        navigate('/scoring');
    }

    const onSuccessCheck = (code: number) => {
        if (code === 101 || code === 100) {
            goToScoring();
        } else {
            navigate('/profile');
        }
    }
    
    const onSend = () => {
        if (myRef.current) {
            const inputs = Array.from(myRef.current.querySelectorAll('input'));
            const sms = inputs.reduce((acc, item) => {
                acc += item.value;
                return acc
            }, '');
            if (sms.length === 6) {
                Api?.checkSmsCode(sms, onSuccessCheck);
            }
        }
    }

    useEffect(() => {

        const getHandler = (inputs: HTMLInputElement[], input: HTMLInputElement, idx: number) => {
            return (e: any) => {
                const value = e.target.value;
                if (value.length > 1) {
                    input.value = value.slice(0, 1);
                    if (idx + 1 < inputs.length) {
                        inputs[idx + 1].value = value.slice(1);
                        if (idx + 2 < inputs.length)
                            inputs[idx + 1].focus();
                        else
                            input.blur();
                    } else {
                        input.blur()
                    }
                }
                onSend();
            }
        }

        const getKeyupHandler = (inputs: HTMLInputElement[], input: HTMLInputElement, idx: number) => {
            return (e: any) => {
                if ((e.code === 'Backspace' || e.keyCode === 8) && !input.value) {
                    if (idx - 1 >= 0) {
                        inputs[idx - 1].focus();
                    }
                }
            }
        }

        let inputs: HTMLInputElement[] = [];
        const handlers: EventHandler[] = [];
        const keyupHandlers: EventHandler[] = [];
        if (myRef.current) {
            inputs = Array.from(myRef.current.querySelectorAll('input')) as HTMLInputElement[];
            if (inputs.length > 0) inputs[0].focus();
            inputs.forEach((input, idx) => {
                const handler = getHandler(inputs, input, idx);
                const keyUpHandler = getKeyupHandler(inputs, input, idx);
                handlers.push(handler);
                keyupHandlers.push(handler);
                input.addEventListener('input', handler);
                input.addEventListener('keydown', keyUpHandler)
            })
        }

        return () => {
            inputs.forEach((input, idx) => {
                input.removeEventListener('input', handlers[idx])
                input.removeEventListener('keydown', keyupHandlers[idx]);
            })
        }
    }, [])

    const resendCode = () => {
      const phoneNumber = localStorage.getItem('phoneNumber');
      Api?.getSmsCode(phoneNumber as string);
      setActiveResend(false)
    }

    return (
        <AltPage hasHeader={false}>
            <div className='text-center flex flex-col justify-between pt-[40px] sm:pb-[40px] sm:pb-[80px] sm:pt-[80px] md:pt-[6rem] items-center min-h-[600px] md:min-h-0 flex-grow max-w-[1200px] sm:px-4'>
                <div className="top">
                    <div className="w-[100px] sm:w-[140px] md:w-[100px] mx-auto">
                        <img src="images/profile-blue.svg" alt="profile" />
                    </div>
                    <p className='mt-4 md:mt-3 mb-5 sm:mb-12 md:mb-[5rem] text-[2rem] leading-[2.2rem] sm:text-[2.5rem] sm:leading-[2.8rem] md:text-4xl text-main-blue font-bold'>Введите код из SMS</p>
                </div>
                <div>
                    <p className='tracking-wide md:text-xl mb-3'>Код отправлен на номер <br/>{ phoneNumber }</p>
                    <div ref={myRef} className='flex gap-3 mt-2 w-[100%] md:w-auto px-2 sm:px-0 max-w-[400px] mb-5 md:mb-[6rem] text-center'>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                        <input type="number" className="text-center px-2 sm:px-5 py-[10px] sm:py-[14px] md:py-4 bg-[#E2E2E7] w-[100%] rounded-xl text-2xl md:rounded-[14px] text-form-darken font-bold border-none outline-none"/>
                    </div>
                </div>
                <div className="mb-[6rem] sm:mb-0">
                  { !activeResend && <TimerText time={60} onTimerEnd={() => setActiveResend(true)} /> }
                  { activeResend && <p onClick={resendCode} className="cursor-pointer border transition-all duration-150 border-main-blue py-2 px-4 active:text-white active:bg-main-blue rounded-3xl text-main-blue md:text-xl hover:translate-y-[-2px] transition-transform duration-150">Получить код повторно</p> }
                  {/* <div className="mt-[100px] sm:mt-[140px] w-[100%] md:w-auto md:mt-3 mb-[40px] md:mb-0">
                      <button onClick={onSend} className={`py-2 sm:py-3 md:py-2 px-[90px] md:px-6 min-w-[300px] text-center text-xl sm:text-2xl transition-all select-none font-bold items-center bg-[#007AFF] hover:bg-[#008AFF] px-10 text-[#fff] md:rounded-[22px] rounded-[30px] text-white `}>
                          Продолжить
                      </button>
                  </div> */}
                </div>
            </div> 
        </AltPage>
    );
}

interface ITimerText {
  time: number;
  onTimerEnd: () => void;
}

const TimerText: FC<ITimerText> = ({
  time, onTimerEnd
}) => {
  const [timer, setTimer] = useState(time);
  useEffect(() => {
    if (!timer) {
      onTimerEnd();
    }
    const id = setTimeout(() => {
      if (timer) {
        setTimer(timer - 1);
      } 
    }, 1000);

    return () => clearTimeout(id)
  }, [timer])
  return (
    <p className='tracking-wide md:text-xl text-main-blue'>Отправить новый код через {timer} секунд</p>
  )
}

export default Auth;