import React, { FC } from 'react';

interface IFeedbackCard {
    title: string;
    children?: React.ReactNode;
}

const FeedbackCard: FC<IFeedbackCard> = ({
    title, children
}) => {
    return (
        <div className="shadow-feedback bg-[#F5F5F7] text-left p-[2rem] sm:p-[40px] rounded-[22px] min-w-[300px] h-[100%]">
            {/* <h4 style={{ WebkitTextFillColor: 'transparent' }} className="hidden sm:block text-2xl sm:text-4xl font-medium bg-gradient-custom bg-clip-text">{ title }</h4> */}
            <div className="py-4 sm:pt-5">{ children }</div>
        </div>
    )
}

export default FeedbackCard;
