import React from 'react';
import { useNavigate } from 'react-router-dom';
import { AltPage, Button } from '../../components';

interface FallbackProps {
    reason?: 'noResidence' | 'noPermanent' | 'longDelay' | 'innVerify';
}

const FALLBACK_RESONS = {
    noResidence: "по причине того, что Вы не имеете прописку в г. Душанбе. На данный момент услуги платформы aion работают в пробном режиме и доступны только жителям прописанным в г. Душанбе.",
    noPermanent: "по причине того, что Вы не имеете постоянную прописку в г. Душанбе. На данный момент услуги платформы aion работают в пробном режиме и доступны только жителям поястоянно прописанным в г. Душанбе.",
    longDelay: "по причине того, что Вы допустили более 30 дней просрочек по кредитам за последние 6 месяцев. На данный момент услуги платформы aion работают в пробном режиме и будут доработаны.",
    innVerify: ""
}

const Fallback: React.FC<FallbackProps> = ({ reason = 'noResidence' }) => {
    const navigate = useNavigate();
    return (
        <AltPage hasHeader={false}>
            <div className="flex flex-col justify-center items-center text-center py-5 px-14 max-w-[800px]">
                <div className="bg-[url('images/fallback.svg')] bg-contain bg-center bg-no-repeat w-[100px] h-[100px]"></div>
                <p className="text-4xl text-red-700 font-bold">
                    Заявка не может быть одобрена
                </p>
                <p className="text-2xl my-3 mb-6">
                    { reason && FALLBACK_RESONS[reason] }
                </p>
                <Button type='responsive' onClick={() => navigate('/') }>На главную страницу</Button>
            </div>
        </AltPage>
    )
}

export default Fallback;