import { genderOptions, loanPurposeOptions, maritalOptions, registrationOptions } from "../../scoring/statics";
import { IReviewOption, TElement } from "../components";

interface IReviewData {
    title: string;
    key: string;
    type: TElement;
    name?: string;
    subType?: string;
    options?: IReviewOption[]
}

export const validators: any = {
    amount: (value: number) => (value >= 1000 && value <= 10000),
    duration: (value: number) => (value >= 1 && value <= 32 ),
    dependants: (value: number) => (value >= 0 && value <= 15),
    dob: (val: string) => {
      const value = String(val);
      if (!value) return false;
      const date = new Date(value);
      const arr = value.split('-');
      if (arr.length !== 3 || (date.toString() === "Invalid Date") || (date.getTime() > Date.now()))
        return false;
      return true
    }
}

