import { ACCESS_TOKEN, ACCESS_TOKEN_DATE } from "../constants";
import { axios } from "./";

export interface IApi {
  getSmsCode: (phoneNumber: string) => void;
  checkSmsCode: (sms: string, callback: (code: number) => void) => void;
  getProfile: () => void;
  fillScoreData: (data: Record<string, string | number> | any) => void;
  fillResidence: (data: any) => void;
  verifyInn: (data: any) => Promise<any>;
  mainFeedback: (data: any) => void;
  scoringFeedback: (data: any) => void;
  uploadPass: (data: any) => void;
  uploadSelfie: (data: any, navigate?: any) => void;
  chosenBank: (data: any) => void;
  getScore: (navigate?: any) => void;
  getScoreData: (after?: any) => void;
}

export interface IScoreData {
  amount: string;
  dushanbe_registration_type: string;
  fact_address: string;
  fact_address_orientir: string;
  sector: string;
  consumer_credit_goal: string;
  duration: string;
  credit_history_count_spitamen: string;
  credit_history_count_ibt: string;
  credit_history_count_imon: string;
  credit_history_count_finca: string;
  past_bad_days: string;
  age: string;
  gender: string;
  marital_status: string;
  dependants: string;
  mon_income: string;
}

export interface IScore {
  scoring_status: number;
  add_terms: string;
  interest_rate: number;
  monthly_payment: number;
  passport_photo: number;
  commission: number;
}

export type IScoreKey = 'spitamen' | 'imon' | 'finca' | 'ibt';

export type StoreKey = "phoneNumber" | "loaderOn" | "toastOn";
export interface IStore {
  isAuthorized?: boolean;
  phoneNumber?: string;
  loaderOn?: boolean;
  toastOn?: boolean;
  toastMsg?: string;
  fallbackReason?: 'noResidence' | 'noPermanent' | 'longDelay' | 'innVerify';
  scoreData?: Record<string, string | number> | any;
  score?: Record<IScoreKey, IScore>;
  qrcode?: number;
  scorePassed?: boolean;
  blockScoring?: boolean;
  choosenBank?: {
    name: string;
    nameEng: string;
  }
}

interface IScoreServer {
  response_code: number;
  pass_code: number;
  results: Record<IScoreKey, IScore>
}

export const getApi = (store: any, setStore: any) => {
  const updateStore = (keyValue: IStore) => {
    Object.entries(keyValue).forEach(([key, value]) => {
      store[key as StoreKey] = value;
    });
    console.log(store);
    setStore({ ...store });
  };

  const request = async (url: string, data: any) => {
    await axios
      .post(url, data)
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };

  const showToast = (toastMsg: string) => {
    updateStore({ toastMsg, toastOn: true });
    setTimeout(() => updateStore({ toastOn: false }), 4000);
  };

  const getSmsCode = (phoneNumber: string) => {
    const data = { phone_number: phoneNumber };
    updateStore({
      phoneNumber,
      loaderOn: true,
    });
    axios
      .post("/send_sms_code?", data)
      .then((res) => {
        updateStore({ loaderOn: false });
        localStorage.setItem("phoneNumber", phoneNumber);
      })
      .catch((err) => {
        updateStore({ loaderOn: false });
      });
  };

  const MSG: any = {
    104: 'Неверный код доступа',
    106: 'Вы были заблокированы по причине ввода существующего ИНН'
  }
  const checkSmsCode = (sms: string, callback: (code: number) => void) => {
    const data = {
      phone_number: store.phoneNumber,
      code: sms,
    };
    updateStore({ loaderOn: true });
    axios
      .post("/check_sms_code", data)
      .then((res: any) => {
        const {
          jwt_access_token: token,
          jwt_token_type: tokenType,
          response_code: statusCode,
        } = res;
        const curr: IStore = {};
        curr.loaderOn = false;
        curr.isAuthorized = true;
        curr.scorePassed = statusCode === 102;
        if (statusCode > 103) {
          showToast(MSG[statusCode]);
        } else {
          localStorage.setItem(ACCESS_TOKEN, token);
          localStorage.setItem(ACCESS_TOKEN_DATE, Date.now() + '')
          axios.defaults.headers.common[
            "Authorization"
          ] = `${tokenType} ${token}`;
          updateStore(curr);
          callback(statusCode);
        }
      })
      .catch((err) => {
        console.log(err);
        updateStore({ loaderOn: false });
        showToast("Ошибка на сервере");
      })
      .finally(() => {
        updateStore({ loaderOn: false });
      })
  };

  const getProfile = () => {
    axios
      .get("/profile")
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };

  const getScoreData = async  (after?: any) => {
    if (after && after.withLoader) {
      updateStore({ loaderOn: true });
    }
    await axios
      .get("/get_score_data")
      .then((res: any) => {
        const qrcode = res.user_data ? res.user_data.pass_code : null;
        updateStore({ scoreData: res.user_data, qrcode, scorePassed: Boolean(qrcode && qrcode.length), loaderOn: false })
        if (after) {
          after.navigate();
        }
      })
      .catch((err) => {
        updateStore({ loaderOn: false })
        console.log(err)
      });
  };

  const getScore = async (navigate?: any) => {
    updateStore({ loaderOn: true });
    axios
      .get("/score")
      .then((response: any) => {
        updateStore({ loaderOn: false });
        const res = response as IScoreServer;
        if (res && res.response_code === 100) {
          store.qrcode = res.pass_code;
          store.score = res.results;
          setStore({ ...store });
          navigate('/greetings')
        }
      })
      .catch((err) => console.log(err))
      .finally(() => updateStore({ loaderOn: false }))
  };

  const verifyInn = (data: any) => {
    return axios
      .post('/verify_inn', data)
  }

  const chosenBank = (payload: any) => {
    const { data, choosenBank } = payload;
    updateStore({ choosenBank })
    axios.post('/chosen_bank', data)
      .then(res => console.log(res))
      .catch(err => console.log(err))
      .finally(() => updateStore({ loaderOn: false }))
  }

  return {
    getSmsCode,
    checkSmsCode,
    getProfile,
    fillResidence: async (data: any) => {
      const scoreData = Object.assign(store.scoreData, data);
      updateStore({ scoreData });
      await request("/fill_residence", data)
    },
    fillScoreData: async (data: Record<string, string | number>) => {
      const scoreData = Object.assign(store.scoreData || {}, data.data);
      console.log(scoreData)
      updateStore({ scoreData });
      await request("/fill_score_data", data);
    },
    verifyInn,
    mainFeedback: (data: any) => void request('/main_feedback', data),
    scoringFeedback: (data: any) => void request('/scoring_feedback', data),
    uploadPass: (data: any) => void request('/upload_passport', data),
    uploadSelfie: async (data: any, navigate?: any) => {
      updateStore({ loaderOn: true })
      console.log('49787')
      await request('/upload_selfie', data)
      console.log('kjsdlkfjsl')
      updateStore({ loaderOn: false })
      navigate('/qrcode')
    },
    getScore,
    getScoreData,
    chosenBank
  };
};
