import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { AltPage, Loader, Modal } from '../../components';
import { useUpdateStore } from '../../misc/customHooks';

const Logout: React.FC<{}> = () => {

    const navigate = useNavigate();
    const updateStore = useUpdateStore();

    useEffect(() => {
        localStorage.clear();
        updateStore && updateStore({ qrcode: undefined, scorePassed: undefined, scoreData: {}, blockScoring: false, score: undefined })
        navigate('/login')
    }, [])

    return (
        <AltPage hasHeader={true}>
            <Modal onClose={() => {}}>
                <Loader />
            </Modal>
        </AltPage>
    )
}

export default Logout;