import React, { FC } from "react";
interface MainCardProps {
  title: string;
  body: string | FC;
  img: any;
  bodyPrefix?: string;
  name?: string;
}

const MainCard = ({
  title,
  body,
  bodyPrefix = "Скоринг",
  img,
  name,
}: MainCardProps) => (
  <div className="flex flex-col items-center text-center mx-auto md:mx-0 pt-[40px] sm:pt-[60px] shadow-light-v2 md:pt-[40px] md:pb-[40px] xl:pt-[70px] pb-[30px] sm:pb-[50px] px-1 xl:px-[30px] rounded-[40px] md:rounded-[22px] bg-white max-w-[370px] xl:max-w-[360px] md:max-w-[270px] lg:max-w-[300px] xl:min-w-[300px]">
    <div className="max-w-[110px] md:max-w-[90px] min-h-[130px]">
      <img src={img} alt={name || "icon"} />
    </div>
    <div className="flex-3 w-[100%] pt-4">
      <div className="font-bold inline-block text-4xl md:text-3xl xl:text-6xl lg:my-5 mb-3">{title}</div>
      <div className="text-2xl md:text-xl xl:text-3xl block mb-7 px-2">
        {bodyPrefix && <b>{bodyPrefix}:&nbsp;</b>}
        <span className="inline-block">{body}</span>
      </div>
    </div>
  </div>
);

export default MainCard;
