import React, { FC, useEffect, useRef, useState } from 'react';
import { monthOptions } from '../../components/score/static';

export type TElement = 'input' | 'select' | 'date';

export interface IReviewOption {
  label: string;
  value: string | number;
}

interface IReviewElement {
    name?: string;
    title: string;
    type?: TElement;
    subType?: string;
    isValid?: boolean;
    value: string | boolean;
    options?: IReviewOption[];
    onChange: (value: string) => void;
}

interface IReviewSelect {
  options: IReviewOption[];
  value: string;
  widthClass?: string;
  paddingClass?: string;
  onChange: (val: string) => void;
}

const ReviewSelect: FC<IReviewSelect> = ({
  options = [],
  value,
  widthClass = 'min-w-[100%] max-w-[350px]',
  paddingClass = 'px-4 sm:pl-8 sm:pr-14 py-2 sm:py-2',
  onChange
}) => {
  const [open, setOpen] = useState(false);
  const optionRef: React.RefObject<HTMLDivElement> = useRef(null);

  useEffect(() => {
    
    const handler = (e: any) => {
      const target = e.target as HTMLElement;
      const val = target.dataset.value;
      if (val) {
        onChange(val);
      }
    }

    if (optionRef.current) {
      optionRef.current.addEventListener('click', handler)
    }

    return () => {
      if (optionRef.current) {
        optionRef.current.removeEventListener('click', handler);
      }
    }
  }, [])

  const optionVal = options.find(item => item.value == value);
  return (
    <div
      onClick={() => setOpen(!open)}
      className={`relative cursor-pointer rounded-xl bg-[#F5F5F7] ${widthClass} sm:w-fit ${paddingClass} text-[#555] outline-none text-scoring`}>
      <p className={`whitespace-nowrap text-ellipsis overflow-hidden py-1`}>{ (optionVal && optionVal.label) || "Выберите" }</p>
      <span className={`absolute top-[50%] transition-transform duration-300 translate-y-[-50%] rotate-[90deg] text-[#444] scale-x-[1.3] right-[20px] text-4xl ${(open && 'rotate-270') || ''}`}>&#8250;</span>
      <div
        ref={optionRef}
        className={`options shadow-select transition-max-height duration-200 overflow-y-scroll hidden-scroll max-h-[150px] sm:max-h-[200px] overflow-hidden absolute left-0 top-[90%] z-50 rounded-bl-xl rounded-br-xl bg-[#F5F5F7] w-[100%] px-4 ${paddingClass} sm:pb-2 sm:pt-1 border-t border-[#777] ${open ? 'max-h-[200px]' : 'max-h-0 invisible' }`}
      >
        { options.map(({label, value}, idx) => (
          <React.Fragment key={value}>
            <p data-value={value} className="transition-opacity duration-200 hover:opacity-60 leading-[1.5rem] my-2 sm:my-3 cursor-pointer">{ label }</p>
            { idx + 1 < options.length && <hr className="mb-2 sm:mb-3 border-[#777]" /> }
          </React.Fragment>
        )) }
      </div>
    </div>
  )
}

/**
 * ReviewDatePicker
 * @returns 
 */
const defaultDate = {
  year: "", month: "", day: ""
}
interface IReviewDatePicker {
  value: string;
  onChange: (value: string) => void;
}
const ReviewDatePicker: FC<IReviewDatePicker> = ({
  value, onChange
}) => {

  const [dob, setDob]= useState<any>(defaultDate)
  const validation = {
    'day': (value: number) => (value > 0 && value <= 31),
    'year': (value: number) => (value >= 1970 && value <= 2022)
  }


  const isLeapYear = (year: number) => {
    if (year % 4 === 0) {
      return (year % 100 === 0) ? (year % 400 === 0) : true;
    }
    return false;
  }

  const getWL = (val:number) => (((val < 10 && '0') || '') + val)

  const changeDate = (e: any, key: string, value?: string) => {
    const days = [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    dob[key] = value !== undefined ? value : e.target.value;
    const { year, month, day } = dob;
    if (isLeapYear(parseInt(year)) && month == 2 && days[month - 1] <= day) {
      dob.day = 28;
    } else if (days[month - 1] < day) {
      dob.day = days[month - 1] < day ? days[month - 1] : day;
    } else {
      dob.day = day <= 0 ? '' : day;
    }
    onChange(`${year}-${getWL(month)}-${getWL(dob.day)}`)
    setDob({ ...dob });
  }

  const validationCalsses = (key: 'day' | 'year', value: string) => {
    if (!validation[key](parseInt(value))){
      return "border border-rose-600"
    }
    return ""
  }

  useEffect(() => {
    const arr = value && value.split('-');
    if (arr.length > 2) {
      const dob: any = {};
      const day = parseInt(arr[2]);
      const month = parseInt(arr[1]);

      dob.year = arr[0];
      dob.month = month ? month : '';
      dob.day = day ? day : '';
      setDob(dob);
    }
  }, [value])

  return (
    <div className="w-[100%] flex items-center justify-between gap-2 lg:gap-1 xl:gap-2 max-w-[330px] sm:min-w-[370px] sm:max-w-[400px]">
      <div className="text-left h-[100%] max-w-[40px] sm:max-w-[60px]">
        {/* <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">День</span><br /> */}
        <input min={1} max={31} type="number" value={dob.day} onChange={(e: any) => changeDate(e, 'day')} className={`w-[100%] rounded-[10px] text-[#555] bg-[#F5F5F7] focus:outline-none ${validationCalsses('day', dob.day)} px-2 sm:pl-4 sm:pr-3 py-3 text-scoring`} />
      </div>
      <div className="text-left min-w-[160px]">
        {/* <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">Месяц</span><br /> */}
        <ReviewSelect value={dob.month} paddingClass='px-4 sm:pl-5 sm:pr-14 py-2 sm:py-2' widthClass="w-[100%] sm:w-[200px]" options={monthOptions} onChange={(value: string) => changeDate(null, 'month', value)} />
      </div>
      <div className="text-left  h-[100%] max-w-[90px]">
        {/* <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">Год</span><br /> */}
        <input min={1970} max={2022} type="number" value={dob.year} onChange={(e: any) => changeDate(e, 'year')} className={`w-[100%] rounded-[10px] text-[#555] bg-[#F5F5F7] focus:outline-none ${validationCalsses('year', dob.year)}  px-2 sm:pl-4 sm:pr-3 py-3 text-scoring`} />
      </div>
    </div>
  );
}

const ReviewInput: FC<IReviewElement> = ({
  isValid,
  name,
  subType = 'text',
  value,
  onChange
}) => {
  return (
    <input
      onChange={(e: any) => onChange(e.target.value)}
      type={subType}
      id={`score-${name}`}
      name={name}
      value={value as string}
      className={`rounded-xl bg-[#F5F5F7] max-w-[100%] box-border sm:w-[100%] px-4 sm:px-8 py-3 sm:py-3 text-[#555] outline-none text-scoring ${isValid !== undefined && !isValid ? 'border-2 border-rose-600' : ''}`}
      />
  )
}

const getElement = (type: TElement, props: any) => {
  switch(type) {
    case 'input':
      return <ReviewInput {...props} />
    case 'select':
        return <ReviewSelect {...props} />
    case 'date':
      return <ReviewDatePicker {...props} />
  }
}

export const ReviewElement: FC<IReviewElement> = ({
    type = 'input',
    title,
    ...props
  }) => {
    return (
      <div className="flex flex-col w-[100%] box-border">
        <label
          htmlFor={`score-${props.name}`}
          className="text-left text-xl sm:text-2xl text-[#1D1D1F] font-xl mb-2 whitespace-nowrap text-ellipsis overflow-hidden "
        >
          {title}
        </label>
        { getElement(type, props) }
      </div>
    );
}

