import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { AltPage, Button } from '../../components';
import { useApi } from '../../misc/customHooks';
import FlagIcon from '../../assets/media/icons/flag.svg';
import InfoIcon from '../../assets/media/icons/info-icon.svg';
import { collectUserInfo } from './collect';

const defaultChecks = {
    condition_one: true,
    condition_two: true
}

const Login = () => {
    const Api = useApi();
    const navigate = useNavigate();
    const [isAgree, setAgreement] = useState(true);
    const [touched, setTouched] = useState(false);
    const [checks, setChecks] = useState<any>(defaultChecks);

    const [phoneNumber, setNumber] = useState("");
    const onNumberChange = (e: any) => {
        if (e.target.value.length > 9) return;
        setNumber(e.target.value);
    }

    const handleCheckboxes = (e: any) => {
        console.log('clicked: ', e.target.checked)
        const target = e.target as HTMLInputElement;
        const id = target.getAttribute('id') as string;
        checks[id] = target.checked;
        setChecks({ ...checks });
    }

    const validationClasses = (id?: string) => {
        if (id && !checks[id] && touched) return 'error';
        if (touched && (phoneNumber.length !== 9 || 
                phoneNumber.split('').some(chr => (chr < '0' || chr > '9')))) {
            return 'border-2 border-rose-600';
        }
        return '';
    }

    const onSend = () => {
        setTouched(true);
        const arr = Object.values(checks);
        if (arr.length > 1 && arr.every(item => item) && !validationClasses() && phoneNumber.length === 9) {
            collectUserInfo('+992' + phoneNumber);
            Api?.getSmsCode('+992' + phoneNumber);
            navigate('/auth');
        }
    }

    return (
        <AltPage hasHeader={false}>
            <div className='text-center flex flex-col justify-between py-[20px] sm:py-[80px] items-center min-h-[0] sm:min-h-[600px] md:min-h-0 flex-grow max-w-[1200px] px-4'>
                <div className="top">
                    <div className="w-[70px] sm:w-[140px] md:w-[100px] mx-auto">
                        <img src="images/profile-blue.svg" alt="profile" />
                    </div>
                    <p className='mt-4 mb-4 text-[1.7rem] leading-[2.2rem] sm:text-[2.5rem] sm:leading-[2.8rem] md:text-4xl text-main-blue font-bold'>Авторизация пользователя</p>
                    <div className="relative p-2 sm:p-5 pl-10 sm:pl-14 rounded-lg sm:rounded-xl bg-[#DBEEFF] text-sm leading-[16px] sm:text-lg text-left text-[#444] max-w-[550px]  md:mt-3 mb-2 sm:mb-12 md:mb-[4rem]">
                        <span style={{ backgroundImage: `url(${InfoIcon})`}} className="bg-center bg-cover absolute top-2 sm:top-5 left-3 sm:left-5 w-[16px] sm:w-[20px] h-[16px] sm:h-[20px]"></span>
                        Подтвердите номер телефона, с помощью СМС кода. Мы гарантируем безопасность и сохранность ваших данных
                    </div>
                </div> 
                <div>
                    {/* <p className='text-[1.3rem] sm:text-[2rem] tracking-wide font-medium md:text-3xl'>Ваш номер телефона</p> */}
                    <div className={`${validationClasses()} relative mt-2 mb-[0rem] w-[100%] inline-block md:w-[400px] md:mx-auto pl-4 md:px-0 overflow-hidden md:w-[340px] rounded-xl`}>
                        <span className="absolute top-0 left-0 py-3 sm:py-[14px] pl-3 pr-2 md:py-2 text-xl sm:text-xl text-form-darken font-bold bg-form-lighten rounded-bl-xl rounded-tl-xl">
                            +992
                        </span>
                        <input
                            type="number"
                            value={phoneNumber}
                            onChange={onNumberChange}
                            placeholder="Номер телефона"
                            className={`bg-grey-f5 placeholder:text-form text-form-darken placeholder:text-xl placeholder:font-[400] placeholder:tracking-[.5px] tracking-wider px-5 pl-[80px] w-[100%] py-3 sm:py-[14px] md:py-2 rounded-xl text-xl md:rounded-[10px] font-bold border-none outline-none`} />
                    </div>
                </div>
                <div>
                    <div className='mt-2 sm:mb-12 sm:mt-12 md:mt-8 text-left md:max-w-[700px]'>
                        <div className="flex items-start md:items-center mb-5 relative">
                            <input onChange={handleCheckboxes} id="condition_one" type="checkbox" checked={checks.condition_one} className={`${validationClasses('condition_one')} custom-checkbox cursor-pointer bg-[#A9A9A9] rounded-md scale-[1.4]`} />
                            <label htmlFor="condition_one" />
                            <label htmlFor="condition_one" className="ml-4 cursor-pointer text-sm sm:text-lg sm:leading-[1.4rem] md:text-xl">Я прочитал и ознакомился с <a href="https://docs.google.com/document/d/1zzmVRuu4I548DsUOauHrHV9ovinrfTTOvsxCc0z3KtU/edit?usp=sharing" target="_blank" className='text-blue-500' rel="noreferrer">Согласием</a> и <a href="https://docs.google.com/document/d/19Fm2LW5Vnhfwr4BZGq-Ncb6j7eBHcAVWS6H3M5WiIeg/edit?usp=sharing" target="_blank" className="text-blue-500" rel="noreferrer">Правилами обработки</a> моих данных.</label>
                        </div>
                        <div className="flex items-start md:items-center mb-2 relative">
                            <input onChange={handleCheckboxes} id="condition_two" type="checkbox" checked={checks.condition_two} className={`${validationClasses('condition_two')} custom-checkbox cursor-pointer bg-[#A9A9A9] rounded-md scale-[1.4]`} />
                            <label htmlFor="condition_two" />
                            <label htmlFor="condition_two" className="ml-4 cursor-pointer text-sm sm:text-lg sm:leading-[1.2rem] md:text-xl">Я согласен получать <a href="https://docs.google.com/document/d/1zzmVRuu4I548DsUOauHrHV9ovinrfTTOvsxCc0z3KtU/edit?usp=sharing" target="_blank" className='text-blue-500' rel="noreferrer">выгодные уведомления</a> о новых услугах.</label>
                        </div>
                    </div>
                </div>
                <div className="mt-2 sm:mt-[140px] w-[100%] md:w-auto md:mt-3 mb-0 sm:mb-[40px] md:mb-0">
                  <button onClick={onSend} className={`py-3 md:py-2 px-3 sm:px-[90px] md:px-6 min-w-[300px] text-center text-xl sm:text-2xl transition-all select-none font-bold items-center bg-[#007AFF] hover:bg-[#008AFF] px-10 text-[#fff] md:rounded-[22px] rounded-[30px] text-white `}>
                      Подать заявку
                  </button>
                </div>
            </div> 
        </AltPage>
    )
}

export default Login;