import React, { FC, useState } from 'react';

interface IFormContact {
    onSend: (phone: string, feedback: string) => void;
}

const FormContact: FC<IFormContact> = ({
    onSend
}) => {
    const [phone, setPhone] = useState('');
    const [feedback, setFeedback] = useState('');

    const onClick = (e: any) => {
        e.preventDefault();
        onSend(phone, feedback);
        setPhone('');
        setFeedback('');
    }

    return (
        <div className="text-left">
            <div className="group mb-6 sm:mb-4">
                <p className="text-[20px] sm:text-xl pl-3 mb-2 text-black dis-sm:text-custom-grey font-medium">Номер телефона</p>
                <input
                    value={phone}
                    onChange={(e: any) => setPhone(e.target.value)}
                    type="number"
                    placeholder="+992 111 222 333"
                    className="outline-0 py-3 px-4 w-[100%] rounded-[10px] bg-[#fff] dis-sm:bg-grey-f5 text-[14px] sm:text-[16px] placeholder:text-form text-form-darken-100" />
            </div>
            <div className="group mb-6 sm:mb-4">
                <p className="text-[20px] sm:text-xl pl-3 mb-2 text-custom-grey font-medium">Сообщение</p>
                <textarea
                    value={feedback}
                    onChange={(e: any) => setFeedback(e.target.value)}
                    rows={5}
                    
                    placeholder="Сайт работает хорошо только на пк версии на телефонах очень сильно лагает и блоки теряют места"
                    className="resize-none outline-0 py-3 px-4 bg-[#fff] dis-sm:bg-grey-f5 placeholder:text-form text-form-darken-100 text-[14px] leading-[16px] sm:text-[16px] sm:leading-[1.4rem] w-[100%] rounded-[10px]"
                >
                </textarea>
            </div>
            <div className="flex items-center">
                <div className="flex-5 text-[8px] sm:text-sm text-form">
                    <p className="max-w-[170px] sm:max-w-[250px] md:max-w-[330px]">*Эта форма направляет вопросы непосредственно в техническую группу aion</p>
                </div>
                <a onClick={onClick} className="inline-block cursor-pointer ml-auto py-2 px-4 text-[12px] sm:text-lg text-white rounded-[8px] bg-main-blue hover:opacity-75">Отправить</a>
            </div>
        </div>
    )
}

export default FormContact;
