import React from "react";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  IconButton,
  Menu,
  MenuItem,
  Tooltip,
  Avatar
} from "@mui/material";
import { Menu as MenuIcon, Score } from "@mui/icons-material";
import Logo from "../logo";
import { Link } from "react-router-dom";
import { useStore } from "../../../misc/customHooks";

const pages = [
  {
    text: "Преимущества",
    url: '#ourfeatures'
  },
  {
    text: "О нас",
    url: "#aboutus"
  },
  {
    text: "Обратная связь",
    url: '#contacts'
  }];
const settings = [
  {
    text: "Профиль",
    url: "/profile"
  },
  {
    text: "Выйти",
    url: "/logout"
  }
];

const theme = createTheme({
  components: {
    MuiAppBar: {
      styleOverrides: {
        root: {
          backgroundColor: "#fff",
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          color: "#040404 !important",
        },
      },
    },
  },
});

const BUTTON_CLASSES =
  "rounded-[15px] bg-gray-100 flex py-2 text-black items-center gap-2 hover:bg-gray-300 transition-all";

/**
 * Header
 * @returns 
 */
const Header = () => {

  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(
    null
  );
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(
    null
  );

  const store = useStore();

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const { href, origin } = window.location;
  const lastSlash = href.lastIndexOf('/');
  const lastPath = href.slice(lastSlash + 1);
  const isMainPage = href.slice(0, lastSlash) === origin && (!lastPath || lastPath.startsWith('#'));
  const isScoring = lastPath === 'scoring'; 

  return (
    <ThemeProvider theme={theme}>
      <AppBar position="static">
        <div className="px-3 sm:px-[30px] xl:px-[117px]">
          <Toolbar disableGutters>
            <IconButton>{<Logo />}</IconButton>

            <Box sx={{ flex: 1, display: { xs: "flex", md: "none" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block", md: "none" },
                }}
              >
                {isMainPage && pages.map(({ text, url }) => (
                  <MenuItem key={text} href={url} onClick={handleCloseNavMenu}>
                    <Typography textAlign="center">{text}</Typography>
                  </MenuItem>
                ))}
              </Menu>
            </Box>
            {
              isMainPage && (
                <div className="md:flex hidden justify-center flex-grow">
                  {pages.map(({ text, url }) => (
                    <a
                      className="text-center transition-all cursor-pointer block border border-white rounded hover:border-gray-200 text-black hover:bg-gray-200 py-2 px-4"
                      key={text}
                      href={url}
                      onClick={handleCloseNavMenu}
                    >
                      {text}
                    </a>
                  ))}
                </div>
              )
            }

            <div className="flex-grow flex items-center justify-end gap-5">
              {/* <button className={BUTTON_CLASSES + ' px-3 uppercase'}>
                <span>Ru</span>
                <svg
                  width="11"
                  height="9"
                  viewBox="0 0 11 9"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M5.04712 8.7408C5.09758 8.82074 5.1651 8.8861 5.24386 8.93125C5.32263 8.97641 5.41027 9 5.49925 9C5.58823 9 5.67588 8.97641 5.75464 8.93125C5.83341 8.8861 5.90093 8.82074 5.95139 8.7408L10.9018 0.941336C10.9591 0.851376 10.9927 0.746008 10.9989 0.63668C11.0052 0.527352 10.9839 0.418245 10.9372 0.321214C10.8906 0.224183 10.8205 0.142939 10.7346 0.086309C10.6486 0.0296788 10.5501 -0.000171795 10.4496 7.43741e-07H0.548867C0.448673 0.000452158 0.350487 0.0306866 0.264869 0.0874525C0.179251 0.144218 0.109439 0.225368 0.0629416 0.322174C0.0164442 0.418981 -0.00497963 0.527781 0.000974015 0.636876C0.00692766 0.74597 0.0400335 0.85123 0.0967313 0.941336L5.04712 8.7408Z"
                    fill="#1C1C1E"
                  />
                </svg>
              </button> */}
              {
                !isScoring && !store?.scorePassed && (
                  <Link to="/scoring">
                    <button className={BUTTON_CLASSES + ' px-4 hidden sm:block'}>Подать заявку</button>
                  </Link>
                )
              }
              {
                store && store.isAuthorized && (
                  <Tooltip title="Open settings">
                    <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                      <Avatar alt="avatar" src="images/user.svg" />
                    </IconButton>
                  </Tooltip>
                )
              }
              <Menu
                sx={{ mt: "45px" }}
                id="menu-appbar"
                anchorEl={anchorElUser}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorElUser)}
                onClose={handleCloseUserMenu}
              >
                {settings.map(({ text, url }) => {
                  const auth = store?.isAuthorized;
                  if ((!auth) && url === '/logout') {
                    return null;
                  }
                  return (
                    <Link key={text} to={url}>
                      <MenuItem onClick={() => handleCloseUserMenu()}>
                        <Typography textAlign="center">{text}</Typography>
                      </MenuItem>
                    </Link>
                  );
                })}
              </Menu>
            </div>
          </Toolbar>
        </div>
      </AppBar>
    </ThemeProvider>
  );
};
export default Header;
