import React, { FC } from 'react';
import CheckmarkIcon from '../../assets/media/check-mark.png';

interface IOurResults {
    children?: React.ReactNode;
}

export const OurResults: FC<IOurResults> = ({
    children
}) => (
    <>
        <div className="text-left sm:text-center pt-[50px] md:pt-0 max-w-[1200px] mx-auto">
            <div className="inline-block sm:hidden relative">
                <img className="mr-auto mb-[12px] max-w-[50px]" src={ CheckmarkIcon } alt="" />
                <span className="checkAnim light"></span>
            </div>
            <div className="text-[48px] leading-[49px] sm:leading-6xl sm:text-6xl font-bold mb-[20px] md:mb-[2.5rem]">Наши Результаты</div>
            <div className="block sm:hidden w-[90%] xsm:w-[80%] font-medium leading-[18px] text-[15px] mb-[3rem] md:mb-[2rem]">
                Более года мы выдаем микрокредиты в 16 городах и районах по всему
                Таджикистану. Результаты нашего скоринга на основе искусственного интеллекта:
            </div>
            <div className="hidden sm:block leading-[1.7rem] text-2xl mb-[3rem] md:mb-[2rem]">
                <span>
                Более года мы выдаем микрокредиты в 16 городах и районах по всему
                Таджикистану.{" "}
                </span>
                <br />
                <span className={`text-main-blue`}>
                Результаты нашего скоринга на основе искусственного интеллекта:
                </span>
            </div>
        </div>
        <div className="rounded-[30px] sm:rounded-0 py-6 sm:py-0 px-[20px] sm:px-0 shadow-light-v2 sm:shadow-none max-w-[1200px] mx-auto flex flex-wrap md:flex-row flex-col gap-0 sm:gap-[30px] justify-center xl:justify-between">
            { children }
        </div>
    </>
);



interface IOurResultsItem {
    title: string;
    subtitle: string;
    icon: string;
}

export const OurResultsItem: FC<IOurResultsItem> = ({
    title, subtitle, icon
}) => (
    <div className="flex mx-auto min-w-[100%] max-w-[100%] sm:max-w-[280px] sm:mx-auto md:min-w-[320px] min-h-[240px] border-b last:border-0 sm:border-0 sm:min-h-[370px] md:max-w-[320px] sm:rounded-[30px] md:mx-0 mb-0 md:mb-0 pt-0 sm:pt-[5rem] flex-col justify-center sm:shadow-light-v2 xl:shadow-none text-center items-center sm:max-w-[300px]">
        <img className="min-h-[45px] sm:min-g-[66px] max-h-[66px] sm:max-h-[76px] max-w-[128px] sm:max-w-[140px] mb-[30px]" src={icon} alt="tick" />
        <div className="text-[27px] leading-[30px] sm:leading-4xl sm:text-4xl font-bold mb-2 md:mb-0">&gt; { title }</div>
        <div className="text-[18px] leading-[21px] xsm:px-4 sm:px-[80px] md:px-5">{ subtitle }</div>
    </div>
)
