import React, { FC, useState } from 'react';
import { AltPage, ButtonUploader } from '../../components';
import SelfieDefault from '../../assets/media/selfie_default.png';
import { useApi } from '../../misc/customHooks';
import { useNavigate } from 'react-router-dom';

const UploadSelfie: FC<{}> = props => {

    const [imageUrl, setImageUrl] = useState(SelfieDefault);
    const Api = useApi();
    const navigate = useNavigate();

    const onUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files) {
            const formData = new FormData();
            formData.append('selfie', e.target.files[0]);
            Api?.uploadSelfie(formData, navigate);
        }
    }

    return (
        <AltPage>
            <div className="text-center">
                <h4 className="text-4xl text-custom-grey font-bold mb-2">Загрузите селфи с паспортом</h4>
                <p className="text-xl text-dark-grey font-medium mb-5">Сделайте фото себя по образцу ниже</p>
                <div className="max-w-[350px] mx-auto mb-5">
                    <img src={imageUrl} alt="" />
                </div>
                <ButtonUploader className="px-12" onUpload={onUpload}>Загрузить фото</ButtonUploader>
            </div>
        </AltPage>
    )
}

export default UploadSelfie;
