import React from "react";
import robot from "assets/media/robot.svg";
import { Button } from "../../components";
import { useNavigate } from "react-router-dom";
import IphoneImg from '../../assets/media/iphone.png';

const Top = () => {
  const navigate = useNavigate();
  return (
    <div className="min-h-[600px] sm:min-h-[85vh] max-h-[100vh] relative bg-gradient-main-mobile sm:bg-gradient-main md:bg-[url('images/top-bg.svg')] bg-cover bg-center bg-no-repeat overflow-hidden">
      <div className="max-w-[1206px] px-4 xl:px-0 min-h-[600px] sm:min-h-[85vh] flex items-stretch md:items-center justify-center mx-auto overflow-hidden">
       <div className="w-[100%] flex md:flex-nowrap flex-wrap space-x-12 overflow-hidden relative">
          <div className="pt-8 sm:py-12 h-[50%] sm:h-auto">
            <div className="text-white relative mt-14 sm:mt-0 z-40 xl:text-6xl sm:text-6xl text-[27px] leading-[32px] text-center sm:text-left font-bold md:mb-0">
              Подберем Вам кредит до <span className="whitespace-nowrap">10 000</span> сомони сейчас!
            </div>
            <div className="text-white relative z-40 xl:text-3xl sm:text-2xl hidden sm:block mt-14 md:mt-5 mb-[40px] md:mb-0 font-medium">
              <span className="block">Без залога, справок <br className="md:hidden" /> и поручителя.</span>
              <span>Платформа доступна жителям Душанбе.</span>
            </div>
            <div className="block sm:hidden text-white relative z-40 text-lg text-center sm:text-left mt-6 mb-[40px] font-medium">
              Без залога, справок и поручителя. Платформа доступна жителям Душанбе.
            </div>
            <div className="mt-[40px] md:mt-[81px] jusitfy-center hidden sm:flex">
                <Button onClick={() => navigate('/scoring')} size="lg">
                    Подать заявку
                </Button>
            </div>
          </div>
          <div className="md:block hidden min-w-[270px] max-w-[270px]">
            <img src={robot} alt="robot" width={388} height={500}/>
          </div>
       </div>
      </div>
      <div className="absolute bottom-[20px] left-0 z-30 min-w-[100%] max-w-[350px] px-5 box-border mt-[40px] md:mt-[81px] flex jusitfy-center sm:hidden">
          <div onClick={() => navigate('/scoring')} className="py-2 rounded-xl flex-1 block text-center bg-gradient-main-rev shadow-button-light font-bolder text-white text-xl">
              Подать заявку
          </div>
      </div>
      <div className="img absolute bottom-0 left-0 flex justify-center w-[100%]">
        <img src={IphoneImg} alt="" className="block sm:hidden max-h-[300px] xsm:max-h-[340px] m-0" />
      </div>
    </div>
  )
}

export default Top;
