import React from "react";

interface ButtonProps {
  children?: React.ReactChildren | string;
  caption?: string;
  type?: string | 'primary' | 'secondary';
  size?: 'sm' | 'md' | 'lg' | 'xl';
  className?: string;
  disabled?: boolean;
  onClick?: () => void;
}

const PRIMARY_CLASSES = "text-2xl bg-white hover:bg-gray-300 rounded-[80px] text-gray-800";
const SECONDARY_CLASSES = "text-2xl bg-[#007AFF] hover:bg-[#008AFF] px-10 text-[#fff] rounded-[22px] text-white";
const RESPONSIVE = "text-xl sm:text-2xl bg-[#007AFF] hover:bg-[#008AFF] px-10 text-[#fff] rounded-[22px] text-white"
const SIZE = {
  sm: 1, md: 2, lg: 3, xl: 6
}

const TYPES = {
  primary: PRIMARY_CLASSES,
  secondary: SECONDARY_CLASSES,
  responsive: RESPONSIVE
} as any;

const Button = ({ children, caption, type = 'primary', size = 'md', disabled = false, className = 'min-w-[250px]', onClick }: ButtonProps) => {
  return (
    <button disabled={disabled} onClick={onClick} className={`py-${SIZE[size]} ${(disabled && 'opacity-60') || ''} px-6 ${className} mx-auto md:mx-0 text-center transition-all select-none font-bold items-center ${TYPES[type]}`}>{caption || children}
      <span className="py-2 py-4 py-6 hidden"></span>
    </button>
  );
}

export default Button;
