import React,{ FC } from "react";
import CheckmarkIcon from '../../assets/media/check-mark.png';

/**
 * Advantages
 */
interface IAdvantages {
    title: string;
    subtitle: string;
    children?: React.ReactNode;
}

export const Advantages: FC<IAdvantages> = ({
    title, subtitle, children
}) => {
    return (    
        <div className="text-white pt-[60px] md:pt-0 max-w-[1200px] mx-auto sm:px-0">
            <div className="text-left md:text-center">
              <div className="text-[2rem] xsm:text-[2.5rem] sm:text-6xl font-bold mb-2">{ title }</div>
              <div className="text-[18px] xsm:text-[20px] sm:text-3xl">{ subtitle }:</div>
            </div>
            <div className="relative flex flex-col items-start md:items-center lg:flex-row gap-[30px] md:gap-[50px] lg:gap-[120px] my-[30px] pt-[30px] sm:pt-[60px] lg:my-[100px] justify-center">
            { children }
            </div>
            <span className="xl:top-two xl:top-three hidden"></span>
        </div>
    )
}

/**
 * AdvantagesItem
 */
interface IAdvantagesItem {
    text: string;
    children?:React.ReactNode;
    order: string;
}

const staticPositions: Record<string, string> = {
    two: "40px",
    three: "80px"
}

export const AdvantagesItem: FC<IAdvantagesItem> = ({
    text, children, order
}) => (
    <div className={`flex flex-col mb-8 md:mb-0 md:flex-row w-auto sm:w-[450px] md:w-[350px] md:space-x-3 md:items-center relative xl:top-${order}`}>
        <img className="hidden sm:block md:mr-0 mr-auto md:mb-0 mb-3 max-w-[85px] md:max-w-[90px]" src={`images/${order}.svg`} alt="one" />
        <div className="inline-block sm:hidden">
            <img className="mr-auto mb-[20px] max-w-[50px]" src={ CheckmarkIcon } alt="" />
            <span className="checkAnim"></span>
        </div>
        <div className="font-semibold text-[25px] leading-[30px] sm:leading-[2.2rem] sm:text-[2rem] pl-0 md:text-lg">{ text }</div>
        { children }
    </div>
)
