import React, { FC, useContext, useEffect, useRef, useState } from 'react';
import { AltPage, Button, FeedbackCard, FormContact, FormRate, Modal } from '../../components';
import UserIcon from '../../assets/media/icons/user.png';
import QRCode from 'react-qr-code';
import { ContextApi, ContextStore } from '../../App';
import { Score } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useApi, useStore } from '../../misc/customHooks';

const QRcode: FC<{}> = props => {
    const [isModalOn, setModal] = useState(true);
    const [rate, setRate] = useState(0);
    const Api = useApi();
    const store  = useStore();
    const navigate = useNavigate();

    const sendFeedback = (phone: string, feedback: string) => {
        const data = {
            text: feedback,
            rating: rate
        }
        Api?.scoringFeedback(data);
        setModal(false)
    }
    const bankName = (store?.choosenBank && store.choosenBank.name) || "";
    return (
        <AltPage hasHeader>
            <div className="text-center max-w-[600px] py-8">
                <h4 className="text-4xl text-main-blue font-bold mb-6">Код Доступа</h4>
                <p className="text-lg leading-[1.25rem]">Вам выдается QR и 6-значный код с которым Вы сможете получить кредит наличными в { bankName }.</p>
                <QRGenerator value={store?.qrcode || 0} />
                <div className=" py-3 px-4 sm:py-6 sm:px-8 text-[12px] md:text-lg uppercase font-medium rounded-[15px] mb-8 mx-3 shadow-custom-light">
                    У ВАС ЕСТЬ <b>10 ДНЕЙ</b> ЧТОБЫ ПОСЕТИТЬ { bankName } В Г. ДУШАНБЕ.
                    СПИСОК ФИЛИАЛЬНОЙ СЕТИ { bankName } ДОСТУПЕН <span className="text-main-blue cursor-pointer">ЗДЕСЬ.</span>
                     В ФИЛИАЛЕ ВЫ ДОЛЖНЫ ПРЕДСТАВИТЬСЯ КАК КЛИЕНТ AION 
                     И ПРЕДОСТАВИТЬ 6-ЗНАЧНЫЙ КОД ДЛЯ ПОЛУЧЕНИЯ КРЕДИТА НАЛИЧНЫМИ.
                </div>

                <div className="flex py-3 px-4 sm:py-6 sm:px-8 rounded-[15px] mb-4 md:mb-8 mx-3 shadow-custom-light gap-[15px]">
                    <img className="max-w-[50px] max-h-[50px]" src={UserIcon} alt="" />
                    <p className="text-sm md:text-lg md:leading-[1.5rem] text-left">Теперь у Вас есть личный кабинет, в котором доступна вся информация по кредитной заявке.</p>
                </div>

                <Button
                    onClick={() => navigate('/profile')}
                 type="responsive">Войти в личный кабинет</Button>
            </div>
            {
                isModalOn && (
                    <Modal onClose={() => setModal(false)}>
                        <div className="p-5 md:p-12 box-border">
                            <h3 className="text-2xl mb-2 sm:text-4xl font-bold text-center md:mb-[40px]">Оцените и оставьте свои комментарии</h3>
                            <div className="flex pb-5 md:pb-0 flex-col-reverse md:flex-row justify-center mt-5 md:gap-[20px] gap-[10px] w-[98%] mx-auto">
                                <div className="flex-5">
                                    <FeedbackCard title="Feedback">
                                        <FormContact onSend={sendFeedback} />
                                    </FeedbackCard>
                                </div>
                                <div className="flex-4">
                                    <FeedbackCard title="Оцените">
                                        <FormRate onChange={setRate} />
                                    </FeedbackCard>
                                </div>
                            </div>
                        </div>
                    </Modal>
                )
            }
        </AltPage>
    )
}

interface IQRGenerator {
    value: number;
}

const QRGenerator: FC<IQRGenerator> = ({
    value
}) => {
    return (
        <div className="flex md:flex-row flex-col max-w-[600px] mx-auto my-8 md:my-10 gap-[20px] md:gap-[50px] justify-center">
            <div className="w-[185px] mx-auto md:mx-0 rounded-[20px] shadow-custom p-5">
                <QRCode size={140} value={value.toString()} fgColor="#077AFF" />
            </div>
            <div className="p-5 md:p-10 mx-auto md:mx-0 text-custom-grey rounded-[20px] max-w-[300px] shadow-custom">
                <h2 className="text-4xl md:text-6xl font-bold">{ value }</h2>
                <p className="text-xl md:text-3xl">сохраните код</p>
            </div>
        </div>
    )
}

export default QRcode;
