import React, { FC, useCallback, useEffect, useLayoutEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Spinner, Button, ScoreInput, AltPage } from "../../components";
import { IScoreData, IStore } from "../../misc/api";
import { useApi, useStore } from "../../misc/customHooks";
import { dependantsOptions, genderOptions, loanPurposeOptions, maritalOptions, registrationOptions } from "../scoring/statics";
import { ReviewElement, TElement } from "./components";
import { reviewData, validators } from "./static";

interface IReview {
  scoreData: IScoreData
}

const Review: FC<IReview> = ({ scoreData }) => {
  const [data, setData] = useState<any>(scoreData);
  const [loaderOn, setLoader] = useState(false);
  const api = useApi();
  const navigate = useNavigate();

  const handler = (key: string, value: string) => {
    data[key] = value;
    setData({ ...data });
  }

  const decrease = (obj: any, key: string) => {
    if (obj) {
      obj[key] = String(parseInt(obj[key]) - 1)
    }
  }

  useLayoutEffect(() => {
    decrease(scoreData, 'credit_history_count_spitamen')
    decrease(scoreData, 'credit_history_count_imon')
    setData({ ...scoreData })
  }, [scoreData]);

  const withPromise = ((fn: any) => {
    return new Promise(async (res, rej) => {
      await fn();
      res("OK")
    })
  })

  const onSend = () => {
    Object.keys(data).forEach(key => {
      if (data[key] === null) {
        delete data[key];
      }
    })
    const { 
      dob,
      age,
      dushanbe_registration_type,
      fact_address,
      fact_address_orientir,
      ...scoringData
     } = data;

     const residenceData = {
      dushanbe_registration_type, fact_address, fact_address_orientir
     }

     setLoader(true)
     Promise.all([
      withPromise(async () => await api?.fillScoreData({ data: scoringData })),
      withPromise(async () => await api?.fillResidence(residenceData))
     ])
     .then((res) => {
        setLoader(false);
        api?.getScore(navigate);
     })
     .catch(err => {
        setLoader(false);
     })
  }

  const isEmpty = (val: any) => {
    return (val === undefined || val === '' || val === null);
  }

  const isButtonDisabled = () => {
    if (!data) return true;
    return reviewData.some(({key})=> (validators[key] && data ? !validators[key](data[key]) : isEmpty(data[key])))
  }

  return (
    <AltPage loaderOn={loaderOn}>
      <div className="px-4 md:px-10 lg:px-14 pt-4 sm:pt-10 max-w-[1300px]">
        <div className="text-center">
          <h1 className="text-2xl lg:text-3xl xl:text-5xl font-bold text-blue-500">
            Подтверждение данных
          </h1>
          <div className="my-3">
            <img src="images/divider.svg" alt="divider" width="100%" />
          </div>
          <p className="text-lg leading-[1.2rem] md:leading-[1.5rem] md:text-xl lg:text-3xl">
            Проверьте ответы на предыдущие вопросы. Вы можете изменить ответы
            нажав на кнопки ниже. Далее нажмите “Получить ответ” для расчета
            результата Вашей заявки.
          </p>
        </div>
        { data && (
          <div className="mt-10 px-auto review-grid gap-y-4 gap-x-0 sm:gap-x-8 lg:gap-6">
            {
              reviewData.map((item) => {
                const { key } = item;
                if (data[key] === undefined) return null;
                return (
                  <ReviewElement
                    value={data[key]}
                    onChange={(val: string) => handler(key, val)}
                    isValid={validators[key] ? validators[key](data[key]) : !isEmpty(data[key]) }
                    { ...item }
                    />
                )
              })
            }
          </div>
        )}
        <div className="my-10 text-center" >
            <Button onClick={onSend} disabled={isButtonDisabled()} caption="Получить ответ" type="secondary" />
        </div>
      </div>
    </AltPage>
  );
};

export default Review;
