import React from "react";
import { Box, IconButton } from "@mui/material";
import { ReactComponent as FacebookIcon } from "icons/facebook.svg";
import { ReactComponent as InstagramIcon } from "icons/instagram.svg";
import { ReactComponent as LinkedInIcon } from "icons/linkedin.svg";
import Logo from "../logo";


const Footer = () => (
  <div className="py-3 px-[20px] lg:px-[117px] shadow-sm flex justify-between items-center ">
    <div className="text-[8px] md:text-lg">© aion, all rights reserved {new Date().getFullYear()}</div>
    <div className="inline-block mr-5">
      <Logo />
    </div>
    <Box className="flex justify-center gap-1 sm:gap-3 sm:ml-3 sm:ml-0">
      <IconButton style={{padding: '0'}} className="p-0">
        <FacebookIcon className="w-[20px] sm:w-auto" />
      </IconButton>
      <IconButton style={{padding: '0'}} className="p-0">
        <InstagramIcon className="w-[20px] sm:w-auto" />
      </IconButton>
      <IconButton style={{padding: '0'}} className="p-0">
        <LinkedInIcon className="w-[20px] sm:w-auto" />
      </IconButton>
    </Box>
  </div>
);

export default Footer;
