import React from 'react';
import Loader from '../../loader';
import Header from '../header';

interface AltPageProps {
    hasHeader?: boolean;
    loaderOn?: boolean;
    isScoring?: boolean;
}

const AltPage: React.FC<AltPageProps> = ({
    children,
    hasHeader = true,
    loaderOn = false,
    isScoring = false
}) => (
    <div className={`${!isScoring ? 'bg-[url(\'images/bg.svg\')]' : 'bg-gradient-scoring'} flex flex-col min-h-screen box-border`}>
        <span className="hidden bg-[url('images/bg.svg')]"></span>
        {hasHeader && (
            <Header />
        )}
        <div className={`bg-white flex-grow ${isScoring ? 'rounded-[20px]' : 'rounded-[30px]'} md:rounded-[50px] justify-center items-center p-4 m-3 my-4 sm:m-6 sm:my-8 md:m-12 flex flex-col`}>
            {children}
        </div>
        { loaderOn && <Loader showLoader={loaderOn} /> }
    </div>
)

export default AltPage;