import React, { FC, useEffect, useRef, useState } from "react";
import classNames from "classnames/bind";
import { InputProps } from "@mui/material";

interface ScoreButtonProps {
  label: string;
  isSelected: boolean;
  onClick?(): void;
}

interface ScoreTextProps {
  text: string;
  size?: string | "sm" | "md" | "lg";
  className?: string;
}

interface ScoreInputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  name: string;
  label: string;
  type?: string;
  validationMsg?: string;
  isValid?: boolean;
}

const ScoreButton: React.FC<ScoreButtonProps> = ({
  label,
  isSelected,
  onClick,
}) => (
  <div
    className={classNames(
      "bg-[#F5F5F7] px-3 py-2 inline-block sm:block sm:px-8 sm:py-3 score-btn-rounded cursor-pointer text-scoring min-w-[80px] md:min-w-[300px] select-none",
      isSelected ? "bg-[#007aff] text-[#fff]" : "text-[#666666]"
    )}
    onClick={onClick}
  >
    {label}
  </div>
);

const SCORE_TEXT_SIZES = {
  sm: "text-xl",
  md: "text-3xl",
  lg: "text-5xl",
} as any;

const ScoreText: React.FC<ScoreTextProps> = ({
  text,
  size = "md",
  className,
}) => (
  <p
    className={`text-left text-scoring ${className}`}
  >
    {text}
  </p>
);

const ScoreInput: React.FC<ScoreInputProps> = ({
  name,
  label,
  type = "text",
  isValid,
  validationMsg,
  ...props
}) => {
  return (
    <div className="flex flex-col w-[100%]">
      <label
        htmlFor={`score-${name}`}
        className="text-left text-scoring text-[#1D1D1F] font-xl mb-2"
      >
        {label}
      </label>
      <input
        type={type}
        id={`score-${name}`}
        name={name}
        className={`score-btn-rounded bg-[#F5F5F7] w-[100%] sm:w-fit px-4 sm:px-10 py-2 sm:py-3 text-[#007AFF] outline-none text-scoring ${isValid !== undefined && !isValid ? 'border-2 border-rose-600' : ''}`}
        {...props}
      />
      { validationMsg && <p className="text-sm text-rose-600">{ validationMsg }</p> }
    </div>
  );
}

const ScorePhone: React.FC<Omit<ScoreInputProps, "label">> = ({
  name,
  validationMsg,
  type = "text",
  ...props
}) => (
  <div className="relative">
    <div className="relative flex flex-col score-btn-rounded overflow-hidden">
      <label
        htmlFor={`score-${name}`}
        className="absolute top-0 left-0 h-[100%] py-2 sm:py-4 pl-3 pr-2 flex items-center inline-block text-left text-scoring font-bold tracking-wide text-form-darken bg-form-lighten mb-2"
      >
        +992
      </label>
      <input
        type={type}
        id={`score-${name}`}
        name={name}
        className=" flex-1 box-border sm:pr-10 pl-[5.4rem] sm:pl-[6rem] bg-[#F5F5F7] w-[100%] h-[100%] sm:w-auto px-4 py-2 sm:py-4 tracking-wider text-form-darken outline-none text-scoring"
        {...props}
      />
    </div>
    { validationMsg && <p className="absolute bottom-[-24px] text-sm text-rose-600 italic pl-3">{ validationMsg }</p> }
  </div>
);

interface SelectOption {
  label: string;
  value: any;
}
interface IScoreSelect {
  value: string;
  widthClass?: string;
  options: SelectOption[];
  onChange: (e: any) => void;
}

const ScoreSelect: FC<IScoreSelect> = (props) => {
  const { options = [],
    value,
    widthClass = "min-w-[300px] max-w-[350px] sm:w-fit",
    onChange
  } = props;
  const [open, setOpen] = useState(false);
  const optionRef: React.RefObject<HTMLDivElement> = useRef(null);

  useEffect(() => {
    
    const handler = (e: any) => {
      const target = e.target as any;
      const val = target.dataset.value;
      target.value = val;
      if (val) {
        onChange(e);
      }
    }

    if (optionRef.current) {
      optionRef.current.addEventListener('click', handler)
    }

    return () => {
      if (optionRef.current) {
        optionRef.current.removeEventListener('click', handler);
      }
    }
  }, [onChange])

  const optionVal = options.find(item => item.value == value);
  return (
    <div
      onClick={() => setOpen(!open)}
      className={`relative cursor-pointer score-btn-rounded bg-[#F5F5F7] ${widthClass} px-4 sm:pl-8 sm:pr-14 py-2 sm:py-3 text-[#555] outline-none text-scoring`}>
      <p className={`whitespace-nowrap text-ellipsis overflow-hidden py-1 sm:py-0`}>{ (optionVal && optionVal.label) || "Выберите" }</p>
      <span className={`absolute top-[50%] transition-transform duration-300 translate-y-[-50%] rotate-[90deg] text-[#444] scale-x-[1.3] right-[20px] text-4xl ${(open && 'rotate-270') || ''}`}>&#8250;</span>
      <div
        ref={optionRef}
        className={`options shadow-select z-40 transition-max-height duration-200 max-h-[150px] md:max-h-[240px] overflow-y-scroll overflow-hidden hidden-scroll absolute left-0 top-[90%] z-50 rounded-bl-xl rounded-br-xl bg-[#F5F5F7] w-[100%] px-4 sm:pl-8 sm:pr-14 py-2 sm:py-3 sm:pb-2 sm:pt-1 border-t border-[#777] ${open ? 'max-h-[400px]' : 'max-h-0 invisible' }`}
      >
        { options.map(({label, value}, idx) => (
          <React.Fragment key={value}>
            <p data-value={value} className="transition-opacity duration-200 hover:opacity-60 leading-[1.5rem] my-2 sm:my-3 cursor-pointer">{ label }</p>
            { idx + 1 < options.length && <hr className="mb-2 sm:mb-3 border-[#777]" /> }
          </React.Fragment>
        )) }
      </div>
    </div>
  )
}


const ScoreInputDays: React.FC<Omit<ScoreInputProps, "label">> = ({
  name,
  type = "text",
  ...props
}) => (
  <div className="flex items-center gap-2 justify-start">
    <input
      { ...props }
      type="number"
      min="0"
      className="sm:w-[20%] max-w-[200px] sm:max-w-[300px] rounded-[22px] bg-[#F5F5F7] focus:outline-none focus:border-1 pl-10 pr-5 py-1 sm:py-3 text-scoring"
      id="data"
    />
    <label htmlFor="data" className="text-lg sm:text-2xl">
      дней
    </label>
  </div>
);

export { ScoreButton, ScoreText, ScoreInput, ScorePhone, ScoreInputDays, ScoreSelect };
