import {URL, AUTH_TOKEN, PHONE, ACCESS_TOKEN, ACCESS_TOKEN_DATE} from '../constants';
import { axios } from './axios';

export function request(url, data, method = 'get') {
    const TOKEN = getStoredData();
    const options = {
        method: method.toUpperCase(),
        body: JSON.stringify(data),
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${TOKEN}`
        }
    }
    return fetch(URL + url, options)
}

export function formatPhone(str) {
    return String(str).replaceAll(/[- ]/ig, '');
}

export function storeData(data, name = AUTH_TOKEN) {
    try {
        localStorage.setItem(name, JSON.stringify(data))
    } catch (e) {
        console.error(e);
    }
}

export function getStoredData(name = AUTH_TOKEN) {
    if (typeof window !== 'undefined') {
        try {
            const item = localStorage.getItem(name);
            if (item) {
                return JSON.parse(item);
            } else {
                return null;
            }
        } catch (e) {
            console.error('Error: ', e);
            return null;
        }
    }
}

export function removeStoredData(name = AUTH_TOKEN) {
    localStorage.removeItem(name);
}

export function logout() {
    removeStoredData();
    window.location.href = '/login';
}

export const isAuthorized = !!getStoredData(AUTH_TOKEN);

export const checkPhoneString = (phone) => {
    return formatPhone(phone).length === 13
}

const COLORS = ['#D85140', '#F2C94C', '#219653'];
export const getProgressColor = (value = 0, maxValue = 100, threshold = 3) => {
    return COLORS[Math.floor(Number(value) / (maxValue / threshold))];
}


export const handleSmoothScroll = (url) => {
    document.querySelector(url).scrollIntoView({behavior: 'smooth'});
};

export const downloadQR = (id = 'qrcode') => {
    const svg = document.getElementById(id);
    const svgData = new XMLSerializer().serializeToString(svg);
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    const img = new Image();
    img.onload = function () {
        canvas.width = img.width;
        canvas.height = img.height;
        ctx.drawImage(img, 0, 0);
        const pngFile = canvas.toDataURL("image/png");

        const downloadLink = document.createElement("a");
        downloadLink.download = id;
        downloadLink.href = `${pngFile}`;
        downloadLink.click();
    };

    img.src = "data:image/svg+xml;base64," + btoa(svgData);
};

export function copy(string) {
    navigator.clipboard.writeText(string);
}

export const getToken = () => {
    const accessToken = localStorage.getItem(ACCESS_TOKEN);
    const createDate = localStorage.getItem(ACCESS_TOKEN_DATE);

    const diff = createDate && ((Date.now() - createDate) / 36e5);
    if (accessToken &&  diff < 2) {
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + accessToken;
        return true;
    }
    return false;
}

export const getCache = (store) => {
    const phoneNumber = localStorage.getItem(PHONE);
    store.isAuthorized = getToken();
    if (phoneNumber) {
        store.phoneNumber = phoneNumber;
    }
    return (store.isAuthorized || store.phoneNumber);
}