import {getStoredData} from "./";
import axs from 'axios';
import {ACCESS_TOKEN, ACCESS_TOKEN_DATE, URL} from '../constants';
import { getCache } from "./cookies";
export {CancelToken} from 'axios';

export const axios = axs.create({
    baseURL: 'https://aion-backend.azurewebsites.net',
});

axios.defaults.headers.common['Accept-Language'] = 'ru';

axios.interceptors.request.use(function (config) {
    const data = getStoredData();
    let token = data;
    if (!!data && typeof data === 'object') {
        token = data.token;
        config.headers['Authorization'] = `Bearer ${token}`
    }
    return config;
}, function (error) {
    return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
    return response.data;
}, function (error) {
    return Promise.reject(error);
});
