import React, { FC, useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import {
    ScoreCard,
    ScoreButton,
    ScoreText,
    ScoreInput,
    ScoreSelect,
    ScorePhone,
    ScoreInputDays,
  } from "../../components";
import { useApi, useUpdateStore } from '../../misc/customHooks';
import { contactOptions, dependantsOptions, expensesOptions, genderOptions, loanPurposeOptions, maritalOptions } from './statics';

/**
 * Credit summ
 */
const CreditSummTags = ['amount']
const CreditSumm = () => {
    const ERR_MSG = "Ваш кредитный запрос превышает установленный лимит в 10 000 сомони. На данный момент услуги платформы aion работают в пробном режиме и доступны для получения кредитов до 10 000 сомони."

    const isValid = (value: string) => {
      const curr = Number(value);
      if (curr < 1000 || curr > 10000) {
        return false;
      }
      return true;
    }

    return (
        <ScoreCard
            centered
            title="Сумма кредита"
            subtitle="Введите сумму в поле ниже "
            subtitleLabel="от 1 000 до 10 000 сомони"
            type="finance"
            tagName={CreditSummTags[0]}
            isValid={isValid}
            errorMsg={ERR_MSG}
        />
    )
}

/**
 * Place of residence
 */
const ResidenceOptionTags = ['dushanbe_registration_type'];
const ResidenceOption: FC = () => {
    const [selected, setSelected] = useState<boolean | null>(null);
    const [isPermanent, setIsPermanent] = useState<boolean | null>(null);
    const [district, setDistrict] = useState("");
    const [districtOrient, setDistrictOrient] = useState("");
    const Api = useApi();
    const navigate = useNavigate();
    const updateStore = useUpdateStore();

    const setRegister = (val: boolean) => {
      setSelected(val);
      if (!val) {
        setIsPermanent(null)
        setDistrict("")
        setDistrictOrient("")
      }
    }
  
    const CustomComponent = (
      <div>
        <ScoreText text="Есть ли у Вас прописка в г. Душанбе?" />
        <div className="flex justify-start gap-5 mb-6 mt-3">
          <ScoreButton
            label="Да"
            isSelected={selected === true}
            onClick={() => setRegister(true)}
          />
          <ScoreButton
            label="Нет"
            isSelected={selected === false}
            onClick={() => setRegister(false)}
          />
        </div>
        { selected && (
          <>
            <ScoreText
              text="Выберите тип прописки в г. Душанбе (из списка ниже)"
            />
            <div className="flex justify-start gap-5 mt-3">
              <ScoreButton
                label="Постоянная"
                isSelected={isPermanent === true}
                onClick={() => setIsPermanent(true)}
              />
              <ScoreButton
                label="Временная"
                isSelected={isPermanent === false}
                onClick={() => setIsPermanent(false)}
              />
            </div>
            <div className="mt-6">
              <ScoreInput
                label="Укажите фактический адрес проживания в г. Душанбе"
                name="test"
                value={district}
                placeholder="ул. Рудаки 24"
                onChange={(e: any) => setDistrict(e.target.value)}
              />
            </div>
            <div className="mt-6">
              <ScoreInput
                label="Укажите ориентир фактического адреса проживания в г. Душанбе"
                name="test"
                value={districtOrient}
                placeholder="ЦУМ"
                onChange={(e: any) => setDistrictOrient(e.target.value)}
              />
            </div>
          </>
        ) }
      </div>
    );

    const registrationType = (!selected && 'not dushanbe') || (isPermanent ? 'permanent' : 'temporary');
    const callOnNext = () => {
      const data = {
        "dushanbe_registration_type": registrationType,
        "fact_address": district,
        "fact_address_orientir": districtOrient
      }
      Api?.fillResidence(data);
      if (registrationType === 'not dushanbe') {
        updateStore && updateStore({ fallbackReason: 'noResidence', blockScoring: true })
        navigate('/fallback');
      }
    }

    const isPageValid = () => {
      const isSelectedNull = selected === null;
      const isPermanentNull = isPermanent === null;
      if (isSelectedNull || (selected === true && isPermanentNull) ||
            (selected === true && !isPermanentNull && (!district || !districtOrient))) {
              return false;
            }
      return true;
    }

    return (
      <ScoreCard
        isPageValid={isPageValid()}
        title="Место жительства"
        type="custom"
        customComponent={CustomComponent}
        errorMsg = {((!selected && 'noResidence') || (!isPermanent && 'noPermanent')) as string}
        callOnNext={callOnNext}
      />
    );
};


