/**
 * Loan history options
 * @returns 
 */
interface IBankRow {
    name: string;
    src?: string;
    value: string;
    onChange: (e: any) => void;
}

const BankRow: FC<IBankRow> = ({ name, src, value, onChange }) => (
    <div className="flex gap-5">
        <div className="bg-white shadow-custom-sm max-h-[70px] rounded-xl text-[#666666] p-2 sm:p-5 justify-between items-center flex max-w-[350px] w-full">
            <span className="text-lg sm:text-2xl">{name}</span>
            {!!src && <div className="bg-contain bg-no-repeat bg-center w-[30px] h-[30px] sm:w-[50px] sm:h-[50px]" style={{backgroundImage: `url('${src}')`}} />}
        </div>
        <input
            type="number"
            onChange={onChange}
            className="py-2 sm:py-5 pl-4 sm:px-6 pr-3 text-lg text-center sm:text-2xl bg-[#F5F5F7] text-dark rounded-xl sm:rounded-3xl w-[50px] sm:w-[100px] outline-none"
            value={value}
            />
    </div>
);

const LoanHistoryOption = () => {
    const [selected, setSelected] = useState<boolean | null>(null);
    const [spitamen, setSpitamen] = useState("0");
    // const [finca, setFinca] = useState("0");
    // const [ibt, setIbt] = useState("0");
    const [imon, setImon] = useState("0");
  
    const CustomComponent = (
      <div>
        <ScoreText text="Получали ли Вы ранее кредит в любом банке или МДО?" />
        <div className="flex gap-5 mt-3 mb-5 justify-start">
          <ScoreButton
            label="Да"
            isSelected={selected === true}
            onClick={() => setSelected(true)}
          />
          <ScoreButton
            label="Нет"
            isSelected={selected === false}
            onClick={() => setSelected(false)}
          />
        </div>
        { selected && <ScoreText text="Укажите количество полученных кредитов в следующих двух банках/МДО:" /> }
        {
          selected && (
            <div className="flex flex-col gap-3 mt-3">
              <BankRow
                value={spitamen}
                onChange={(e: any) => setSpitamen(e.target.value)}
                name="Спитамен Банк"
                src="images/spitamen-short.svg" />
              {/* <BankRow
                value={finca}
                onChange={(e: any) => setFinca(e.target.value)}
                name="FINCA"
                src="images/finca-short.svg" /> */}
              <BankRow
                value={imon}
                onChange={(e: any) => setImon(e.target.value)}
                name="ИМОН Интернешнл"
                src="images/imon-short.svg" />
              {/* <BankRow
                value={ibt}
                onChange={(e: any) => setImon(e.target.value)}
                name="IBT"
                src="images/ibt-short.svg" /> */}
            </div>
          )
        }
      </div>
    );

    const data = {
      credit_history_count_spitamen: spitamen,
      // credit_history_count_ibt: ibt,
      credit_history_count_imon: imon,
      // credit_history_count_finca: finca
    }

    return (
      <ScoreCard
        title="Кредитная история"
        type="custom"
        valueOuter={data}
        customComponent={CustomComponent}
      />
    );
};

/**
 * Loan history options
 * @returns 
 */
const LoanHistoryDelayTags = ['past_bad_days']
const LoanHistoryDelay = () => {
    const [selected, setSelected] = useState<boolean | null>(null);
    const [value, setValue] = useState("0");
    const updateStore = useUpdateStore();
    const navigate = useNavigate();

    const onChange = (e: any) => {
      const val = e.target.value;
      setValue(val);
    }
  
    const CustomComponent = (
      <div>
        <ScoreText text="Есть ли у Вас текущий кредит или рассрочка сейчас?" />
        <div className="flex justify-start gap-5 mt-3 mb-5">
          <ScoreButton
            label="Да"
            isSelected={selected === true}
            onClick={() => setSelected(true)}
          />
          <ScoreButton
            label="Нет"
            isSelected={selected === false}
            onClick={() => setSelected(false)}
          />
        </div>
        <ScoreText text="В течении последних 6 месяцев, в общем сколько дней просрочек Вы допустили по кредитам?" className="mb-3" />
        <ScoreInputDays
            value={value}
            onChange={onChange}
            name="delay"
            placeholder="0"
        />
      </div>
    );

    const ERR_DELAY = "по причине того, что Вы допустили более 30 дней просрочек по кредитам за последние 6 месяцев. На данный момент услуги платформы aion работают в пробном режиме и будут доработаны."
    
    const data = {
      [LoanHistoryDelayTags[0]]: String(value),
      "has_parallel_credit": selected ? "Yes" : "No"
    }

    const callOnNext = () => {
      if (parseInt(value) > 30) {
        updateStore && updateStore({ fallbackReason: 'longDelay', blockScoring: true })
        navigate('/fallback');
      }
    }

    return (
      <ScoreCard
        title="Кредитная история"
        type="custom"
        errorMsg={ERR_DELAY}
        valueOuter={data}
        callOnNext={callOnNext}
        customComponent={CustomComponent}
      />
    );
};


