/**
 * Contacts
 */

const Contacts = () => {
  const options = contactOptions;
  const [phones, setPhones] = useState<any>([
    {
      title: "Укажите, кем этот человек приходится Вам?",
      value: '',
      label: '',
      validationMsg: ''
    },
    {
      title: "Укажите, кем этот человек приходится Вам?",
      value: '',
      label: '',
      validationMsg: ''
    }
  ]);
  const onChange = (idx: number, key: string, value: string) => {
    const obj = phones[idx];
    obj[key] = value;
    setPhones([...phones]);
  }

  const CustomComponent = (
    <div>
        { phones.map((item: any, idx: number) => {
          const noErrors = phones[0].value.length === 9 && phones[1].value.length === 9;
          const isDublicate = noErrors && (phones[0].value === phones[1].value);
          const validtionMsg = (idx > 0 && isDublicate ? 'Одинаковые контактные номера' : '')
          return (
            <div className="mb-5" key={idx}>
              <ScoreText text={`Контакт ${idx + 1}`}></ScoreText>
              <div className="flex flex-col justify-center sm:justify-start sm:flex-row gap-3">
                <div className={`w-[300px] ${validtionMsg ? 'mb-5' : ''}`}>
                  <ScorePhone
                    name={`extraPhone_${idx}`}
                    value={item.value}
                    validationMsg={validtionMsg}
                    onChange={(e: any) => onChange(idx, 'value', e.target.value)}
                  />
                </div>
                <ScoreSelect
                  value={item.label}
                  options={options}
                  onChange={(e: any) => void onChange(idx, 'label', e.target.value)}
                />
              </div>
            </div>
          )
        })}
    </div>
  )

  const data = phones.reduce((acc: any, phone: any, idx: number) => {
    acc[`contact_phone_number_${idx + 1}`] = "+992" + phone.value;
    acc[`contact_type_${idx + 1}`] = phone.label;
    return acc;
  }, {});

  const isPageValid = () => {
    if (!phones.every((item: any) => !!(item.label && item.value && item.value.length === 9))) {
      return false;
    }
    return phones[0].value.trim() !== phones[1].value.trim();
  }

  const callOnNext = () => {
    let noErrors = true;
    phones.forEach((item: any) => {
      if (item.value.trim().length !== 9) {
        item.validationMsg = 'Номер телефона состоит из 9 цифр'
        noErrors = false;
      }
    })
    if (noErrors && phones[0].value.trim() === phones[1].trim()) {
      phones[1].validationMsg = 'Одинаковые контактные номера'
    }
    setPhones([ ...phones ]);
  }

  return (
    <ScoreCard
          title="Дополнительные контактные номера"
          subtitle=""
          centered={true}
          isPageValid = {isPageValid()}
          type="custom"
          valueOuter={data}
          callOnNext={callOnNext}
          customComponent={CustomComponent}
        />
  )
}


/**
 * Identity check options
 * @returns 
 */
const IdentityCheckOption = () => {
  const [fio, setFio] = useState('');
  const [inn, setInn] = useState('');
  const Api = useApi();
  const [fioValid, setFioValid] = useState<any>({});
  const [innValid, setInnValid] = useState<any>({});
  const updateStore = useUpdateStore();
  const navigate = useNavigate();

  const checkValidFio = (value: string) => {
    const arr = value.split(' ').filter(item => item);
    if (fioValid.touched && arr.length < 2) {
      fioValid.isValid = false;
    } else {
      fioValid.isValid = arr.every(item => (item.length >= 2));
    }
    fioValid.touched = true;
    setFio(value);
    setFioValid({ ...fioValid })
  }

  const checkValidInn = (value: string) => {
    if (innValid.touched && value.length !== 9) {
      innValid.isValid = false;
    } else {
      innValid.isValid = true;
    }
    innValid.touched = true;
    setInn(value);
    setInnValid({ ...innValid })
  }

    const CustomComponent = (
        <div className="space-y-5">
            <ScoreInput
              value={fio}
              onChange={(e: any) => checkValidFio(e.target.value)}
              isValid={fioValid.isValid}
              name="fio"
              label="Фамилия Имя Отчество"
              placeholder="Айонзода Айон"></ScoreInput>
            <ScoreInput
              value={inn}
              onChange={(e: any) => checkValidInn(e.target.value)}
              isValid={innValid.isValid}
              type='number'
              name="inn"
              label="ИНН/РМА"
              placeholder="000000000"></ScoreInput>
        </div>
    )
    
    const callOnNext = () => {
      if (!fioValid.isValid || !innValid.isValid) return;
      const [name, surname, patronymic] = fio.split(' ');
      const data = {
        name, surname, patronymic, inn
      }
      Api?.verifyInn(data)
        .then((res: any) => {
          if (res.response_code === 101) {
            updateStore && updateStore({ fallbackReason: 'innVerify' })
            navigate('/fallback')
          } else {
            Api?.getScoreData({
              withLoader: true,
              navigate: () => navigate('/review')
            });
          }
        })
        .catch(err => console.log(err))
    }
    return (
        <ScoreCard
          title="Проверка личности"
          subtitle="Введите свое ФИО и ИНН/РМА "
          subtitleLabel="9-значное число на задней стороне паспорта"
          type="custom"
          callOnNext={callOnNext}
          nextPageExist={false}
          customComponent={CustomComponent}
        />
      );
}


