import React from 'react';
import { Box } from '@mui/material';
import logo from 'assets/media/logo.svg';
import { Link } from 'react-router-dom';
import LogoSm from '../../../assets/media/logo-sm.png';


const Logo = () => (
    <Link to="/">
      <Box className="relative">
        <img src={logo} alt="logo" className="hidden sm:block" height="48" width="110" />
        <img src={LogoSm} alt="" className="w-[55px] block sm:hidden" />
        <span className="absolute z-40 top-[-3px] left-[102%] sm:left-[105%] text-sm sm:text-lg">beta</span>
      </Box>
    </Link>
)

export default Logo;