import React, {FC,  useEffect, useLayoutEffect, useState} from "react";
import { useNavigate } from "react-router-dom";
import { getToken } from "../../misc";
import { useApi, useStore } from "../../misc/customHooks";
import { pages } from "./pages";

type callback = (data?: Record<string, string | number>, skipNextPage?: boolean) => void;
export const ContextNextEvent = React.createContext<callback | null>(null);

interface IScoring {
  startPage?: number
}

const Scoring: FC<IScoring> = ({
  startPage
}) => {
  const [curr, setPage] = useState(0);
  const store = useStore();
  const Api = useApi();
  const navigate = useNavigate();

  const ViewComponent = pages[curr].page;
 
  const { scoreData: score = {} } = store || {};
  const nextPage = (data?: Record<string, string | number>, skipNextPage?:boolean) => {
    if (data) {
      Api?.fillScoreData({ data });
    }
    if (curr < pages.length - 1) {
      setNextUnfilledPage(curr + 1);
    }
  }

  const checkPage = (item: any, idx: number) => {
    const score = store?.scoreData;
    if (!score) return true;
    return score[item] === null;
  }

  const canSkip = (idx: number) => {
    const score = store?.scoreData;
    if (!score) return false;
    const [key, value] = Object.entries(pages[idx].conditions || {})[0] || [];
    if (key) {
      console.log('KEY: ', key, value, JSON.stringify(score))
      return score[key] !== value;
    }
    return false
  }

  const setNextUnfilledPage = (page?: number) => {
    let idx = page || curr;
    while (idx < pages.length - 1) {
      const skip = canSkip(idx);
      console.log("CanSkip", skip)
      if (!skip && pages[idx].tags.every(item => checkPage(item, idx))) {
        return setPage(idx);
      }
      idx++;
    }
    setPage(idx);
  }

  useEffect(() => {
    if (!getToken() && !store?.isAuthorized) {
      navigate('/login');
    } else if (store?.scorePassed || store?.blockScoring) {
      navigate('/profile');
    }
  }, [store?.isAuthorized, store?.blockScoring, store?.scorePassed])

  useLayoutEffect(() => {
    console.log('ksjdlfjl')
    setNextUnfilledPage()
  }, [pages])

  return (
    <div>
      <ContextNextEvent.Provider value={nextPage}>
        <ViewComponent />
      </ContextNextEvent.Provider>
    </div>
  )
};

export default Scoring;
