import React, { useContext, useLayoutEffect, useState } from "react";
import { AltPage, Button, Modal, ScoreButton, ScoreSelect } from "../../components";
import { ContextNextEvent } from "../../pages/scoring";
import { monthOptions } from "./static";
interface SelectOption {
  label: string;
  value: any;
}

export interface IValidationParam {
  key: string;
  value: string
}

interface ScoreCardProps {
  title: string;
  subtitle?: string;
  subtitleLabel?: string;
  type?: string;
  options?: SelectOption[];
  customComponent?: any;
  defaultValue?: string | number;
  errorMsg?: string;
  valueOuter?: Record<string, string | number>;
  tagName?: string;
  isPageValid?: boolean;
  nextPageExist?: boolean;
  autoRedirect?: boolean;
  centered?:boolean;
  isValid?: (value: string) => boolean;
  callOnNext?: () => void;
  canSkipNext?: (value: string) => boolean;
}

const defaultDate = {
  year: "", month: "", day: ""
}

const ScoreCard: React.FC<ScoreCardProps> = ({
  title,
  subtitle,
  subtitleLabel,
  type,
  options,
  customComponent,
  centered,
  errorMsg,
  valueOuter,
  tagName,
  isPageValid,
  defaultValue,
  nextPageExist = true,
  autoRedirect = false,
  isValid,
  callOnNext,
  canSkipNext
}) => {
  const [state, setState]: any = useState({ radioSelected: null });
  const [value, setValue] = useState<number | string>(defaultValue || "");
  const [modalOn, setModal]= useState(false);
  const [animClass, setAnimClass] = useState('');
  const [dob, setDob]= useState<any>(defaultDate)
  const validation = {
    'day': (value: number) => (value > 0 && value <= 31),
    'year': (value: number) => (value >= 1970 && value <= 2022)
  }

  const onChange = (val: string | number) => {
    setValue(val)
  };

  const handleRadio = (value: string) => {
    onChange(value)
    setState((prev: any) => ({
      ...prev,
      radioSelected: value,
    }));
  };

  const nextPage = useContext(ContextNextEvent);

  const getWL = (val:number) => (((val < 10 && '0') || '') + val)

  const next = () => {
    let localVal = value;
    if (type === 'date') {
      const { year, month, day } = dob;
      localVal = `${year}-${getWL(month)}-${getWL(day)}`
    }
    if (isValid && !isValid(localVal as string)) {
      setModal(true);
    } else if (nextPage) {
      const skip = canSkipNext && canSkipNext(localVal as string)
      if (valueOuter !== undefined) {
        nextPage(valueOuter, skip);
      } else if (tagName) {
        nextPage({[tagName as string]: localVal}, skip)
      } else {
        nextPage();
      }
      callOnNext && callOnNext();
    }
  }

  const beforeNext = () => {
    if (nextPageExist) {
      setAnimClass('fadeLeft');
      setTimeout(() => {
        next();
      }, 200);
    } else {
      next();
    }
  }

  const isLeapYear = (year: number) => {
    if (year % 4 === 0) {
      return (year % 100 === 0) ? (year % 400 === 0) : true;
    }
    return false;
  }

  const changeDate = (e: any, key: string) => {
    const days = [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    dob[key] = e.target.value;
    const { year, month, day } = dob;
    if (isLeapYear(parseInt(year)) && month == 2 && days[month - 1] <= day) {
      dob.day = 28;
    } else if (days[month - 1] < day) {
      dob.day = days[month - 1] < day ? days[month - 1] : day;
    } else {
      dob.day = day <= 0 ? 1 : day;
    }
    setDob({ ...dob });
  }

  const validationCalsses = (key: 'day' | 'year', value: string) => {
    if (!validation[key](parseInt(value))){
      return "border-2 border-rose-600 focus:border-rose-600"
    }
    return ""
  }

  const DatePicker = (
    <div className="w-[100%] px-[10px] flex items-center justify-between gap-2 sm:gap-3 max-w-[400px] sm:max-w-[500px]">
      <div className="text-left max-w-[40px] sm:max-w-[70px]">
        <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">День</span><br />
        <input min={1} max={31} type="number" value={dob.day} onChange={(e: any) => changeDate(e, 'day')} className={`w-[100%] rounded-[10px] bg-[#F5F5F7] focus:outline-none ${validationCalsses('day', dob.day)} px-2 sm:pl-4 sm:pr-3 py-3 sm:py-4 text-scoring`} />
      </div>
      <div className="text-left min-w-[160px]">
        <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">Месяц</span><br />
        <ScoreSelect value={dob.month} widthClass="w-[100%] sm:w-[250px]" options={monthOptions} onChange={(e: any) => changeDate(e, 'month')} />
      </div>
      <div className="text-left max-w-[90px]">
        <span className="inline-block pl-2 mb-2 font-bolder text-[#111] text-xl sm:text-2xl">Год</span><br />
        <input min={1970} max={2022} type="number" value={dob.year} onChange={(e: any) => changeDate(e, 'year')} className={`w-[100%] rounded-[10px] bg-[#F5F5F7] focus:outline-none ${validationCalsses('year', dob.year)}  px-2 sm:pl-4 sm:pr-3 py-3 sm:py-4 text-scoring`} />
      </div>
    </div>
  );

  useLayoutEffect(() => {
    setAnimClass('fadeFromRight')
  }, [title])

  const renderContent = (dob: any) => {
    switch (type) {
      case "select":
        return (
          <div className="inline-block">
            <ScoreSelect
              value={value as string}
              options={options as SelectOption[]}
              onChange={(e: any) => onChange(e.target.value)}
            />
          </div>
        );
      case "input":
      case "finance":
      case "credit-period":
      case "days":
        const labels = {
          finance: 'сомони',
          "credit-period": 'месяц',
          days: "дней",
          input: ""
        }
        let classes = "w-[100%] min-w-[200px] max-w-[200px] sm:max-w-auto sm:w-[240px] rounded-[22px] bg-[#F5F5F7] focus:outline-none focus:border-2 pl-10 pr-5 py-1 sm:py-3 text-scoring ";
        if (isValid && !isValid(value as string)) {
          classes += "border-2 border-rose-600 focus:border-rose-600"
        } else {
          classes += "focus:border-green-400"
        }
        return (
          <div className="flex items-center gap-2 justify-center lg:justify-start">
            <input
              value={value}
              onChange={(e: any) => onChange(e.target.value)}
              type="number"
              min="0"
              className={classes}
              id="data"
            />
            <label htmlFor="data" className="text-scoring">
              { labels[type] }
            </label>
          </div>
        );
      case "radio":
        return (
          <div className="flex flex-col gap-2 min-w-[200px] max-w-[250px]">
            {options?.map((option) => (
              <ScoreButton
                key={option.value}
                label={option.label}
                isSelected={state.radioSelected === option.value}
                onClick={() => handleRadio(option.value)}
              />
            ))}
          </div>
        );
      case "date":
        return DatePicker;
      case "custom":
        return customComponent && customComponent;
    }
  };

  const isButtonDisabled = () => {
    let localVal: string | number | boolean = value;
    if (type === 'date') {
      const { year, month, day } = dob;
      localVal = (validation['day'](day) && validation['year'](year) && month) || '';
    }
    if (isValid && !isValid(localVal as string)) {
      return true;
    } else if (isPageValid !== undefined) {
      return !isPageValid;
    }
    return false;
  }

  if (autoRedirect && !isButtonDisabled()) {
    next()
  }

  return (
    <AltPage isScoring={true}>
      <div className="container overflow-hidden py-[3rem] sm:py-[5rem] min-h-[80vh] justify-start md:justify-center flex flex-col max-w-[1200px]">
        <div className="flex flex-col max-w-[100%] justify-center text-center lg:text-left lg:flex-row gap-5 mx-auto lg:ml-auto lg:min-w-[1000px] max-w-[1000px]">
          <div className="flex justify-center md:block min-w-[150px] relative z-10 max-w-[150px] min-h-[150px] md:min-h-auto rounded-[50%] sm:rounded-[0] bg-gradient-logo md:bg-none md:bg-[#fff] md:min-w-[200px] md:max-w-[200px] mx-auto lg:ml-auto">
            <img className="w-[60%] ml-[15px] md:ml-0 md:w-auto" src="images/robot.svg" alt="robot" />
          </div>
          <div className={`w-[100%] min-h-[400px] sm:min-h-[600px] flex flex-col ${animClass}`}>
            <div>
              <h2 className="text-2xl sm:text-5xl font-bold">{title}</h2>
            
              <div className="mt-6 mb-12">
                <img className="mx-auto lg:mx-0" src="images/line.svg" alt="line" width={100} />
              </div>
              {subtitle && <p className="text-xl sm:text-3xl my-4 mb-14">
                  {subtitle}
                  {subtitleLabel && <span className="text-lg sm:text-2xl">({ subtitleLabel })</span> }
                </p>}
            </div>
            <div className={`min-h-[90px] sm:min-h-[190px] pt-2 mb-5 flex sm:justify-start ${centered ? 'justify-center' : ''} items-start px-5 sm:px-0`}>
              {renderContent(dob)}
            </div>
            { !autoRedirect && (
              <div className={`flex mt-[100px] sm:mt-10 ${centered ? 'justify-center mx-auto' : 'justify-start'} px-5 sm:px-0 lg:justify-start h-[40px] sm:h-[50px] w-[100px]  sm:mx-0`}>
                <Button size="sm" disabled={isButtonDisabled()} type="responsive" className="min-w-[160px] py-1" onClick={beforeNext}>Далее</Button>
              </div>
            )}
          </div>
        </div>
      </div>
    </AltPage>
  );
};

export default ScoreCard;
