import { CircularProgress } from '@mui/material';
import React, { FC, useContext } from 'react';
import { ContextStore } from '../../App';

const Loader: FC<{showLoader?: boolean}> = ({
    showLoader
}) => {
    return (
        <div>
            { showLoader && (
                <div className="fixed top-0 z-50 left-0 bg-overlay-light min-w-[100vw] min-h-[100vh] flex justify-center items-center">
                    <CircularProgress />
                </div>
            )}
        </div>
    )
}

export default Loader;
