import React, { FC, useEffect } from 'react';
import ReactDOM from 'react-dom';
import XmarkIcon from '../../assets/media/xmark.svg';

interface IModal {
    children?: React.ReactNode,
    onClose: () => void;
}

const modalRoot = document.querySelector('#modal')

const Modal: FC<IModal> = ({
    children, onClose
}) => {
    let el: any = document.createElement('div');
    useEffect(() => {
        if (modalRoot) {
            modalRoot.appendChild(el);
        }
        return () => {
            if (modalRoot) {
                modalRoot.removeChild(el as HTMLElement);
            }
        }
    }, [el])

    const onClick = (e: any) => {
        if (e.target.dataset.id === 'modalBox') {
            onClose()
        }
    }

    return (
        ReactDOM.createPortal(
            <div data-id="modalBox" className="fixed overflow-scroll z-50 p-3 md:p-0 top-0 left-0 min-w-[100vw] h-[100vh] bg-overlay flex justify-center md:items-center" onClick={onClick}>
                <div className=" relative py-8 max-w-[900px] min-h-[500px] overflow-y-auto rounded-[24px] bg-white flex justify-center">
                    { children }
                    <div onClick={onClose} className="bg-center bg-cover w-[20px] h-[20px] absolute top-[25px] right-[25px] hover:opacity-75 cursor-pointer" style={{ backgroundImage: `url(${XmarkIcon})` }}></div>
                </div>
            </div>,
            el
        )
    )
}

export default Modal;


