import React, { useEffect } from "react";
import { Page, MainCard, Advantages, AdvantagesItem, OurResults, OurResultsItem, FeedbackCard, FormContact, CardList } from "../../components";
import Top from "./top";
import { Button } from "../../components";
import Nilu from '../../assets/media/icons/nilu.png';
import { useNavigate } from "react-router-dom";
import { useApi } from "../../misc/customHooks";
import ResultsCheck from '../../assets/media/results-check.png';
import ResultsPersons from '../../assets/media/results-persons.png';
import ResultsClock from '../../assets/media/results-clock.png';
import IphoneImg from '../../assets/media/iphone2.png';
import CommentIcon from '../../assets/media/comment.png';

const Section = ({ id, bg, children }: any) => (
  <section id={id} className={`px-[30px] sm:px-[60px] py-[40px] lg:px-[117px] lg:py-[100px] ${bg && bg}`}>{children}</section>
);

const BLUE_COLOR = "#007AFF";

const Main = () => {
  
  const Api = useApi();
  const navigate = useNavigate();

  const sendFeedback = (phone: string, feedback: string) => {
    const data = {
      phone_number: phone,
      text: feedback
    }
    Api?.mainFeedback(data);
  }

  useEffect(() => {
    const anims = ['checkAnim', 'fadeRight'].reduce<any>((acc, item) => {
      const arr = Array.from(document.querySelectorAll('.' + item)) as HTMLElement[];
      acc = acc.concat(arr.map(node => ({ el: node, cls: item })));
      return acc;
    }, []);

    const handler = () => {
        const windowHeight = window.innerHeight;
        const visibility: any = {
          checkAnim: 20,
          fadeRight: 100
        }
        anims.forEach((el: any) => {
          const item = el.el as HTMLElement;
          const elementVisible = visibility[el.cls];
          const elementTop = item.getBoundingClientRect().top;

          if (elementTop < windowHeight - elementVisible) {
            item.classList.add('active');
          } else {
            item.classList.remove('active');
          }
        });
    };

    window.addEventListener('scroll', handler);
    return () => window.removeEventListener('scroll', handler);
  }, [Nilu])

  return (
    <Page>
      <Top />
      <div className="py-[70px] px-[20px] sm:px-0 sm:py-[80px] bg-[#f5f5f7]">
        <div className="text-center ">
          <div className="text-3xl sm:text-4xl md:text-6xl font-bold mb-1 md:mb-[2rem]">Как работает aion</div>
          <div className="md:text-3xl sm:text-4xl text-2xl">3 шага - 10 минут</div>
        </div>
        <div className="hidden sm:flex flex-col flex-wrap gap-[15px] xl:gap-3 pt-0 sm:pt-9 md:flex-row px-8 sm:px-[80px] md:px-[30px] xl:px-[65px] justify-center xl:space-x-5 mt-[40px] lg:mt-[2rem]">
          <MainCard
            title="Шаг 1"
            body="моментальный ответ по кредитной заявке"
            bodyPrefix="Скоринг"
            img="images/list.svg"
            name="list"
          />
          <MainCard
            title="Шаг 2"
            body="проверка личности для оформления кредита"
            bodyPrefix="Идентификация"
            img="images/profile.svg"
            name="profile"
          />
          <MainCard
            title="Шаг 3"
            body="получение кредита в банке с вашим кодом"
            bodyPrefix="Код доступа"
            img="images/scan.svg"
            name="scan"
          />
        </div>
        <div className="block sm:hidden pt-6 px-4">
          <CardList />
        </div>
      </div>
      <Section id="ourfeatures" bg="bg-black">
        <Advantages title="Преимущества" subtitle="aion позволяет клиентам">
          <AdvantagesItem order="one" text="Получить моментальный ответ по кредиту">
            <div className="absolute w-[150%] top-[110%] left-[30px] hidden xl:block">
              <img src="images/down.svg" alt="arrow-down" width={380} />
            </div>
          </AdvantagesItem>
          <AdvantagesItem order="two" text="Без кредитной истории">
            <div className="absolute w-[150%] top-[-60%] left-[50px] hidden xl:block">
              <img src="images/up.svg" alt="arrow-up" width={380} />
            </div>
          </AdvantagesItem>
          <AdvantagesItem order="three" text="Пропуская очередь в банке и сохраняя время"></AdvantagesItem>
        </Advantages>
      </Section>
      <section className={`px-[30px] sm:px-[60px] py-[40px] lg:px-[117px] lg:pt-[100px] lg:pb-3`}>
        <OurResults>
          <OurResultsItem title="50 000 000" subtitle="сомони успешно выданных микрокредитов" icon={ResultsCheck} />
          <OurResultsItem title="7000" subtitle="довольных клиентов" icon={ResultsPersons} />
          <OurResultsItem title="99%" subtitle="заемщиков платят вовремя по кредитам" icon={ResultsClock} />
        </OurResults>
      </section>
      <section id="aboutus" className={`px-[20px] sm:px-[60px] py-[40px] lg:px-[117px] lg:py-[100px] bg-[#007AFF] sm:bg-white`}>
        <div className="flex lg:flex-row flex-col pt-[40px] sm:pt-[120px] md:pt-0 justify-between items-center gap-0 sm:gap-[100px] max-w-[1200px] mx-auto">
          <div className="w-[100%] lg:w-3/4">
            <div className="hidden sm:block text-6xl font-bold mb-5">
              Наше <span className={`text-[${BLUE_COLOR}]`}>Видение</span>
            </div>
            <div className="block sm:hidden text-center text-6xl font-bold mb-[50px] text-white">
              Наше Видение
            </div>
            <div className="hidden sm:block pr-[18%] text-[1.4rem] leading-[1.7rem] md:text-2xl">
              Мы стремимся стать Вашим главным финансовым советником - от
              получения кредитов до планирования инвестиций. Мы хотим дать Вам
              возможность выбора среди финансовых учреждений. Наше видение
              заключается в следующих трех пунктах (первый из которых мы уже
              реализовываем с Вами):
            </div>
            <div className="hidden sm:block mt-[60px] md:mt-[80px] space-y-[15px] md:space-y-3 pr-0 sm:pr-12 md:pr-0">
              <div className="flex items-start md:items-center gap-3 max-w-[630px] bg-[#F5F5F7] rounded-[22px] py-7 px-5 ">
                <div className="md:w-auto w-[30px] pt-1 md:pt-0">
                  <img src="images/check-blue.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-bold text-[1.3rem] sm:text-[1.5rem] sm:leading-[1.6rem] md:text-2xl mb-2 md:mb-0 pr-2 sm:pr-10 md:pr-0">
                    Решение по заявке на микрокредит онлайн
                  </div>
                  <div className="font-semibold text-lg text-blue-500">
                    Бета тест (пробный период)
                  </div>
                </div>
              </div>
              <div className="flex items-start md:items-center gap-3 max-w-[630px] bg-[#F5F5F7] rounded-[22px] py-7 px-5 ">
                <div className="md:w-auto w-[30px] pt-1 md:pt-0">
                  <img src="images/check-orange.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-bold text-[1.3rem] sm:text-[1.5rem] md:text-2xl mb-2 md:mb-0 pr-2 sm:pr-10 md:pr-0">
                    Автоматизированный трейдинг и инвестиции
                  </div>
                  <div className="font-semibold text-lg text-orange-400">
                    {" "}
                    На стадии разработки
                  </div>
                </div>
              </div>
              <div className="flex items-start md:items-center gap-3 max-w-[630px] bg-[#F5F5F7] rounded-[22px] py-7 px-5 ">
                <div className="md:w-auto w-[30px] pt-1 md:pt-0">
                  <img src="images/check-orange.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-bold text-[1.3rem] sm:text-[1.5rem] md:text-2xl mb-2 md:mb-0 pr-2 sm:pr-10 md:pr-0">
                    Финансовый маркетплейс для выбора банковских продуктов
                  </div>
                  <div className="font-semibold text-lg text-orange-400">
                    {" "}
                    На стадии разработки
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="flex items-center mb-[30px] md:mb-0 md:max-w-auto">
            <img src={IphoneImg} alt="iphone" className="max-w-[210px]" />
          </div>
          <div className="block sm:hidden text-[16px] leading-[20px] text-center text-white">
              Мы стремимся стать Вашим главным финансовым советником - от
              получения кредитов до планирования инвестиций. Мы хотим дать Вам
              возможность выбора среди финансовых учреждений. Наше видение
              заключается в следующих трех пунктах (первый из которых мы уже
              реализовываем с Вами):
            </div>
        </div>
      </section>

      <section className="block sm:hidden px-[30px] xsm:px-[50px] py-[70px]">
        <div>
          <h3 className="text-[40px] leading-[50px] mb-[30px] text-[#1D1D1F] text-center font-bold">Продукты</h3>
          <div className="block sm:hidden space-y-[15px]">
              <div className="flex items-start relative fadeRight gap-3 max-w-[630px] bg-[#FAFAFA] rounded-[12px] py-7 px-5 ">
                <div className="min-w-[13px] max-w-[13px] pt-2">
                  <img src="images/check-blue.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-semibold text-[16px] leading-[20px] mb-[10px] xsm:pr-10">
                    Решение по заявке на микрокредит онлайн
                  </div>
                  <div className="font-semibold text-[12px] leading-[15px] text-blue-500">
                    Бета тест (пробный период)
                  </div>
                </div>
                <span className="absolute right-[-12px] top-[-12px] rounded-[12.5px] w-[25px] h-[25px] flex justify-center items-center text-[15px] leading-[15px] text-white bg-[#0A84FF]">1</span>
              </div>
              <div className="flex items-start relative fadeRight gap-3 max-w-[630px] bg-[#FAFAFA] rounded-[12px] py-7 px-5 ">
                <div className="min-w-[13px] max-w-[13px] pt-2">
                  <img src="images/check-orange.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-semibold text-[16px] leading-[20px] mb-[10px] xsm:pr-10">
                    Автоматизированный трейдинг и инвестиции
                  </div>
                  <div className="font-semibold text-[12px] leading-[15px] text-orange-400">
                    {" "}
                    На стадии разработки
                  </div>
                </div>
                <span className="absolute right-[-12px] top-[-12px] rounded-[12.5px] w-[25px] h-[25px] flex justify-center items-center text-[15px] leading-[15px] text-white bg-[#0A84FF]">2</span>
              </div>
              <div className="flex items-start relative fadeRight gap-3 max-w-[630px] bg-[#FAFAFA] rounded-[12px] py-7 px-5 ">
                <div className="min-w-[13px] max-w-[13px] pt-2">
                  <img src="images/check-orange.svg" alt="checkmark" />
                </div>
                <div>
                  <div className="font-semibold text-[16px] leading-[20px] mb-[10px] xsm:pr-10">
                    Финансовый маркетплейс для выбора банковских продуктов
                  </div>
                  <div className="font-semibold text-[12px] leading-[15px] text-orange-400">
                    {" "}
                    На стадии разработки
                  </div>
                </div>
                <span className="absolute right-[-12px] top-[-12px] rounded-[12.5px] w-[25px] h-[25px] flex justify-center items-center text-[15px] leading-[15px] text-white bg-[#0A84FF]">3</span>
              </div>
            </div>
        </div>
      </section>

      <section id="contacts" className="px-[30px] py-[30px] bg-[#fff] sm:bg-transparent border-t-2 border-[#78788016]">
        <div className="sm:pt-[40px] pb-[100px] md:pt-[40px] md:pb-[150px]">
            <img src={CommentIcon} className="w-[84px] h-[84px] mx-auto my-6" alt="" />
            <h3 className="text-3xl sm:text-4xl text-[#1d1d1f] font-bold text-center mb-[40px]">Оставьте свои коментарии</h3>
            <div className="flex justify-center mt-5 md:gap-[40px] gap-[30px] w-[100%] mx-auto">
                <div className="md:min-w-[800px]">
                    <FeedbackCard title="Обратная связь">
                        <FormContact onSend={sendFeedback} />
                    </FeedbackCard>
                </div>
            </div>
        </div>
      </section>
      <section className="h-[120px] bg-[url('images/bottom-bg.svg')] bg-cover">
        <div className="flex justify-start max-w-[1200px] mx-auto items-center h-[100%] px-[20px]">
          <span className="min-w-[130px] xsm:min-w-[140px]">
            <button onClick={() => navigate('/scoring')} className={`bg-white hover:bg-gray-300 rounded-[30px] md:rounded-[15px] text-gray-800 py-[7px] xsm:py-[13px] md:py-3 md:min-w-[200px] px-[8px] xsm:px-[12px] md:px-10 text-center text-[14px] md:text-2xl transition-all select-none font-bold items-center`}>
              Подайте заявку
            </button>
          </span>
          <span className="text-white text-[10px] xsm:text-[14px] sm:text-xl md:text-2xl lg:text-3xl leading-[18px] md:pl-3 pr-0 sm:pr-[4rem] md:pr-0 ml-2 font-bold">
            и получите ответ по кредиту сейчас
          </span>
          <span className="min-w-[80px] xsm:min-w-[100px] relative md:min-w-[150px] lg:pl-[50px] pr-0 md:pr-0 block">
            <img src={Nilu} className="w-[200px] translate-y-[-30px]" alt="" />
          </span>
        </div>
      </section>
    </Page>
  );
}

export default Main;
