import React, { FC } from 'react';
import QrIcon from '../../assets/media/viewfinder-icon.png';
import SquareIcon from '../../assets/media/square-icon.png';
import PersonIcon from '../../assets/media/person-icon.png';

const cardsData = [
    {
        label: 'Скоринг',
        desc: ' моментальный ответ по кредитной заявке',
        icon: SquareIcon
    },
    {
        label: 'Верификация',
        desc: ' регистрация для оформления кредита',
        icon: PersonIcon
    },
    {
        label: 'QR',
        desc: ' получение кредита с нашим кодом в банке',
        icon: QrIcon
    }
]

const CardList = () => {
    return (
        <div className="cards bg-white px-6 xsm:px-8 py-11 rounded-3xl">
            {
                cardsData.map((item, idx) => (
                    <CardListItem key={idx} step={idx + 1} { ...item } />
                ))
            }
        </div>
    )
}

interface ICardListItem {
    step: number;
    label: string;
    desc: string;
    icon: string;
}

const CardListItem: FC<ICardListItem> = ({
    step, label, desc, icon
}) => {
    return (
        <div className="flex gap-[22px] py-9 last:pb-0 first:pt-0 border-b last:border-0">
            <div className="min-w-[60px] max-w-[60px]">
                <img src={icon} className="max-w-[100%] max-h-[100%]" alt="" />
            </div>
            <div className="flex-1">
                <h4 className="text-3xl font-medium mb-2">Шаг { step }</h4>
                <div className="text-[15px] leading-[18px]">
                    <strong>{ label }:</strong> { desc }
                </div>
            </div>
        </div>
    )
}

export default CardList;
